import React, { useRef, useEffect, useState, useCallback } from 'react';
import BarcodeScanner, { ScanResult } from '../../../utils/BarcodeScanner';

interface Props {
  isActive: boolean;
  onToggle: () => void;
  onBarcodeDetected: (barcode: string) => void;
}

// FIX #2: Debounce constant outside component
const DEBOUNCE_MS = 2000;

const ScannerSection: React.FC<Props> = ({ isActive, onToggle, onBarcodeDetected }) => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const scannerRef = useRef<BarcodeScanner | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string>('');

  // FIX #2: Use refs for debounce state to prevent reset on re-render
  const lastDetectedRef = useRef<string>('');
  const lastDetectedTimeRef = useRef<number>(0);

  // FIX #2: Store callback in ref to avoid effect re-run
  const onBarcodeDetectedRef = useRef(onBarcodeDetected);
  useEffect(() => {
    onBarcodeDetectedRef.current = onBarcodeDetected;
  }, [onBarcodeDetected]);

  useEffect(() => {
    if (isActive && videoRef.current) {
      setIsLoading(true);
      setError('');

      const initScanner = async () => {
        try {
          // Initialize scanner in batch mode (stays open after scan)
          scannerRef.current = new BarcodeScanner(videoRef.current!, {
            desiredWidth: 1280,
            desiredHeight: 720,
            decodeFPS: 20,
            roiResizeLongSide: 600,
            consensusFrames: 1,
          });

          scannerRef.current.onFound((result: ScanResult) => {
            const now = Date.now();
            const barcode = result.text;

            // FIX #2: STRICT DEBOUNCE - same barcode within 2 seconds = SKIP
            if (barcode === lastDetectedRef.current && (now - lastDetectedTimeRef.current) < DEBOUNCE_MS) {
              console.log('[Scanner] DEBOUNCED - same barcode within 2s:', barcode);
              return; // STOP HERE - do not call callback
            }

            // Update refs BEFORE calling callback
            lastDetectedRef.current = barcode;
            lastDetectedTimeRef.current = now;

            console.log('[Scanner] NEW SCAN:', barcode);

            // Call callback via ref (always current version)
            onBarcodeDetectedRef.current(barcode);
          });

          scannerRef.current.onProgress((state) => {
            if (state.phase === 'camera-ready') {
              setIsLoading(false);
            }
          });

          scannerRef.current.onError((err) => {
            console.error('[ScannerSection] Error:', err);
            setError(err.message || 'Gagal mengakses kamera');
            setIsLoading(false);
          });

          await scannerRef.current.start();
        } catch (err: any) {
          console.error('[ScannerSection] Init error:', err);
          setError(err.message || 'Gagal memulai scanner');
          setIsLoading(false);
        }
      };

      initScanner();
    }

    return () => {
      if (scannerRef.current) {
        scannerRef.current.stop();
        scannerRef.current = null;
      }
    };
  }, [isActive]); // FIX #2: Removed onBarcodeDetected from deps - uses ref instead

  if (!isActive) {
    return (
      <div className="scanner-trigger p-4 bg-white border-b border-gray-200">
        <button
          onClick={onToggle}
          className="w-full bg-green-600 hover:bg-green-700 active:bg-green-800 text-white py-4 rounded-xl flex items-center justify-center gap-2 text-lg font-semibold transition-colors"
        >
          <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
          </svg>
          Scan Barcode
        </button>
      </div>
    );
  }

  return (
    <div className="scanner-active relative bg-black">
      {/* Video Preview */}
      <video
        ref={videoRef}
        autoPlay
        playsInline
        muted
        className="w-full h-48 object-cover"
      />

      {/* Loading State */}
      {isLoading && (
        <div className="absolute inset-0 flex items-center justify-center bg-black/50">
          <div className="text-white text-center">
            <svg className="w-8 h-8 mx-auto mb-2 animate-spin" fill="none" viewBox="0 0 24 24">
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
              <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4z" />
            </svg>
            <span className="text-sm">Memulai kamera...</span>
          </div>
        </div>
      )}

      {/* Error State */}
      {error && (
        <div className="absolute inset-0 flex items-center justify-center bg-black/80">
          <div className="text-white text-center px-4">
            <div className="text-red-400 mb-2"></div>
            <p className="text-sm mb-3">{error}</p>
            <button
              onClick={onToggle}
              className="bg-gray-700 text-white px-4 py-2 rounded-lg text-sm"
            >
              Tutup
            </button>
          </div>
        </div>
      )}

      {/* ROI Guide */}
      {!isLoading && !error && (
        <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
          <div className="relative w-64 h-24">
            {/* Corner brackets */}
            <div className="absolute top-0 left-0 w-6 h-6 border-t-2 border-l-2 border-yellow-400"></div>
            <div className="absolute top-0 right-0 w-6 h-6 border-t-2 border-r-2 border-yellow-400"></div>
            <div className="absolute bottom-0 left-0 w-6 h-6 border-b-2 border-l-2 border-yellow-400"></div>
            <div className="absolute bottom-0 right-0 w-6 h-6 border-b-2 border-r-2 border-yellow-400"></div>
            {/* Label */}
            <div className="absolute -top-7 left-1/2 transform -translate-x-1/2 bg-black/70 text-white text-xs px-2 py-1 rounded whitespace-nowrap">
              Arahkan barcode ke sini
            </div>
          </div>
        </div>
      )}

      {/* Close Button */}
      <button
        onClick={onToggle}
        className="absolute top-2 right-2 bg-black/50 hover:bg-black/70 text-white px-3 py-1.5 rounded-full text-sm transition-colors"
      >
         Tutup
      </button>

      {/* Batch Mode Indicator */}
      {!isLoading && !error && (
        <div className="absolute bottom-2 left-2 bg-green-500 text-white text-xs px-2 py-1 rounded-full">
           Mode Cepat
        </div>
      )}
    </div>
  );
};

export default ScannerSection;
