import { getToken, isTokenExpiringSoon, refreshAccessToken } from './auth';

// API base URL from environment variable
// If empty, use relative paths (nginx proxy handles it in production)
// If set, use absolute URL (for development or direct backend access)
const API_BASE_URL = process.env.REACT_APP_API_URL || '';

/**
 * Build full URL from relative path
 * - If API_BASE_URL is set: use absolute URL (development)
 * - If API_BASE_URL is empty: use relative path (production with nginx proxy)
 */
const buildUrl = (url: string): string => {
  // Already absolute URL
  if (url.startsWith('http')) return url;
  // Use base URL if configured (development)
  if (API_BASE_URL) return `${API_BASE_URL}${url}`;
  // Use relative path (production with nginx proxy)
  return url;
};

/**
 * Fetch wrapper with automatic token refresh and retry logic
 * Industry standard: seamless authentication like Claude/ChatGPT
 */
const fetchWithAuth = async (url: string, options: RequestInit = {}, retryCount = 0): Promise<Response> => {
  const MAX_RETRIES = 2;
  
  // Check if token needs refresh before request
  if (isTokenExpiringSoon()) {
    console.log('[fetchWithAuth] Token expiring soon, refreshing...');
    const refreshed = await refreshAccessToken();
    if (!refreshed) {
      throw new Error('Token refresh failed - user logged out');
    }
  }
  
  // Get fresh token
  const token = getToken();
  
  // Make request with token (use buildUrl for consistent URL handling)
  const fullUrl = buildUrl(url);
  const response = await fetch(fullUrl, {
    ...options,
    headers: {
      ...options.headers,
      ...(token && { 'Authorization': `Bearer ${token}` })
    }
  });
  
  // If 401 Unauthorized - try refresh and retry
  if (response.status === 401) {
    if (retryCount < MAX_RETRIES) {
      console.log(`[fetchWithAuth] Got 401, attempting token refresh (retry ${retryCount + 1}/${MAX_RETRIES})...`);
      const refreshed = await refreshAccessToken();
      
      if (refreshed) {
        // Retry request with new token
        const newToken = getToken();
        const fullUrl = buildUrl(url);
        return fetch(fullUrl, {
          ...options,
          headers: {
            ...options.headers,
            ...(newToken && { 'Authorization': `Bearer ${newToken}` })
          }
        });
      } else {
        // Refresh failed - user already logged out by refreshAccessToken()
        throw new Error('Authentication failed - please login again');
      }
    } else {
      console.error('[fetchWithAuth] Max retries reached for 401');
      throw new Error('Authentication failed after retries');
    }
  }
  
  return response;
};

/**
 * Check if user has tenant access
 * Since backend doesn't have /access endpoint, we validate by:
 * 1. Check if token exists
 * 2. Make a test call to tenant chat endpoint
 * Backend will return 403 if user not authorized
 */
export const checkTenantAccess = async (tenantId: string): Promise<boolean> => {
  const token = getToken();
  if (!token) return false;
  
  try {
    const response = await fetchWithAuth(`/api/tenant/${tenantId}/chat`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ message: '__check_access__' })
    });
    
    return response.ok;
  } catch (error) {
    console.error('[checkTenantAccess] Failed:', error);
    return false;
  }
};

/**
 * Get tenant info from backend
 */
export const getTenantInfo = async (tenantId: string) => {
  const endpoint = `/api/tenant/${tenantId}/info`;
  const fullUrl = buildUrl(endpoint);
  const response = await fetch(fullUrl);
  
  if (!response.ok) {
    throw new Error(`Failed to fetch tenant info: ${response.status}`);
  }
  
  return response.json();
};

/**
 * Get user's tenant ID from JWT token
 */
export const getUserTenantId = (): string | null => {
  const token = getToken();
  if (!token) return null;
  
  try {
    const payload = JSON.parse(atob(token.split('.')[1]));
    return payload.tenant_id || null;
  } catch {
    return null;
  }
};

/**
 * Send message to tenant chat endpoint
 * Automatically includes auth token and handles refresh
 */
export const sendTenantMessage = async (tenantId: string, message: string) => {
  const response = await fetchWithAuth(`/api/tenant/${tenantId}/chat`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ message })
  });
  
  if (!response.ok) {
    throw new Error(`API call failed: ${response.status}`);
  }
  
  return response.json();
};

/**
 * Get chat history for user and tenant
 * Returns messages sorted DESC (newest first) - frontend should reverse for UI
 */
export const fetchChatHistory = async (
  userId: string,
  tenantId: string,
  limit: number = 50,
  offset: number = 0
) => {
  const response = await fetchWithAuth(`/chat/history?user_id=${userId}&tenant_id=${tenantId}&limit=${limit}&offset=${offset}`, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json'
    }
  });
  
  if (!response.ok) {
    // Try to get error message from response
    let errorMessage = `Failed to fetch chat history: ${response.status}`;
    try {
      const errorText = await response.text();
      if (errorText) {
        errorMessage += ` - ${errorText.substring(0, 200)}`;
      }
    } catch (e) {
      // Ignore if can't read error text
    }
    throw new Error(errorMessage);
  }
  
  // Read response as text first (can only read once)
  const responseText = await response.text();
  console.log('[fetchChatHistory] Raw response text:', responseText.substring(0, 500));
  
  let data;
  try {
    data = JSON.parse(responseText);
  } catch (e) {
    console.error('[fetchChatHistory] Failed to parse JSON:', e);
    console.error('[fetchChatHistory] Response text:', responseText);
    throw new Error(`Invalid JSON response: ${responseText.substring(0, 200)}`);
  }
  
  console.log('[fetchChatHistory] Parsed response:', data);
  
  // Handle both response formats (with/without nested data)
  if (data.status === 'success' && data.data) {
    return data.data;
  } else if (data.messages) {
    // Fallback for direct messages array
    return {
      messages: data.messages,
      total_count: data.total_count || data.messages.length,
      has_more: data.has_more || false,
      limit,
      offset
    };
  }
  
  // If no messages field, return empty
  if (!data.messages && !data.data) {
    console.warn('[fetchChatHistory] No messages in response:', data);
    return {
      messages: [],
      total_count: 0,
      has_more: false,
      limit,
      offset
    };
  }
  
  return data;
};

/**
 * Send message to customer (public) chat endpoint
 * No authentication required
 */
export const sendCustomerMessage = async (tenantId: string, message: string) => {
  const endpoint = `/${tenantId}/chat`;
  const fullUrl = buildUrl(endpoint);
  const response = await fetch(fullUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({ message })
  });
  
  if (!response.ok) {
    throw new Error(`API call failed: ${response.status}`);
  }
  
  return response.json();
};

/**
 * Get list of tenants for authenticated user
 * Returns array of tenant objects with metadata
 * 
 * Fallback: If /api/user/tenants doesn't exist, use tenant_id from JWT token
 */
/**
 * Get list of tenants for authenticated user
 * 
 * NOT HARDCODED - Uses JWT token to get tenant_id, then fetches tenant info from API
 * Fallback strategy:
 * 1. Try /api/user/tenants (if implemented)
 * 2. If 404, use tenant_id from JWT token
 * 3. Fetch tenant info from /api/tenant/{tenantId}/info for display name
 * 4. If that fails, use basic tenant from JWT
 */
export const getUserTenants = async () => {
  try {
    const response = await fetchWithAuth('/api/user/tenants', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json'
      }
    });
    
    if (!response.ok) {
      // If 404, endpoint not implemented yet - use fallback
      if (response.status === 404) {
        // Don't log as error - this is expected until endpoint is implemented
        return await getTenantsFromJWT();
      }
      // For other errors, still try fallback
      return await getTenantsFromJWT();
    }
    
    const data = await response.json();
    console.log('[getUserTenants] API response:', data);
    
    // Transform backend response to frontend Tenant interface
    const tenants = data.tenants?.map((t: any) => ({
      id: t.tenant_id || t.id,
      name: t.display_name || t.name || t.tenant_id,
      avatar: t.avatar,
      status: t.status || 'offline',
      lastMessage: t.last_message?.content || '',
      lastMessageTime: t.last_message?.timestamp || 'Now',
      role: t.role || 'Owner',
      isPrivate: t.is_private
    })) || [];
    
    console.log('[getUserTenants] Mapped tenants:', tenants);
    return tenants;
  } catch (error: any) {
    // Silently fallback to JWT for any error (404, network, etc.)
    // This is expected behavior until /api/user/tenants is implemented
    return await getTenantsFromJWT();
  }
};

/**
 * Fallback: Get tenant from JWT token (NOT HARDCODED)
 * 
 * This function:
 * 1. Extracts tenant_id from JWT token (from user's login session)
 * 2. Fetches tenant info from /api/tenant/{tenantId}/info API
 * 3. Creates tenant object with real data from API
 * 
 * If API call fails, falls back to basic tenant object using tenant_id from JWT
 */
const getTenantsFromJWT = async (): Promise<any[]> => {
  const tenantId = getUserTenantId();
  if (!tenantId) {
    // No tenant_id in JWT - user not logged in or token invalid
    return [];
  }
  
  // Try to fetch tenant info from API for better display name
  // This is a REAL API call, not hardcoded
  try {
    const tenantInfo = await getTenantInfo(tenantId);
    const tenant = {
      id: tenantId,
      name: tenantInfo.display_name || tenantInfo.name || tenantId.charAt(0).toUpperCase() + tenantId.slice(1),
      avatar: tenantInfo.avatar || '',
      status: 'online' as const,
      lastMessage: '',
      lastMessageTime: 'Now',
      role: 'Owner',
      isPrivate: true
    };
    return [tenant];
  } catch (error) {
    // If getTenantInfo fails, use basic tenant from JWT
    // Still not hardcoded - using tenant_id from JWT token
    const tenant = {
      id: tenantId,
      name: tenantId.charAt(0).toUpperCase() + tenantId.slice(1), // Capitalize first letter
      avatar: '',
      status: 'online' as const,
      lastMessage: '',
      lastMessageTime: 'Now',
      role: 'Owner',
      isPrivate: true
    };
    return [tenant];
  }
};