import React, { useState, useMemo } from 'react';
import BottomNavigation from '../../BottomNavigation';
import InstallPrompt from '../../InstallPrompt';

interface Tenant {
  id: string;
  name: string;
  avatar?: string;
  status?: 'online' | 'offline';
  lastMessage?: string;
  lastMessageTime?: string;
  role?: string;
  isPrivate?: boolean;
  unreadCount?: number;
}

interface TenantSidebarProps {
  tenants: Tenant[];
  activeTenant: Tenant | null;
  onSelectTenant: (tenantId: string) => void;
  isLoading?: boolean;
}

const TenantSidebar: React.FC<TenantSidebarProps> = ({
  tenants,
  activeTenant,
  onSelectTenant,
  isLoading = false
}) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [activeNavTab, setActiveNavTab] = useState('chat');

  // Filter tenants by search query (exclude Milky Assistant as it's hardcoded above)
  const filteredTenants = useMemo(() => {
    const filtered = tenants
      .filter((tenant: Tenant) => tenant.id !== 'milky-assistant')
      .filter((tenant: Tenant) =>
        tenant.name.toLowerCase().includes(searchQuery.toLowerCase())
      );
    return filtered;
  }, [tenants, searchQuery]);

  const handleNavTabChange = (tabId: string) => {
    setActiveNavTab(tabId);
    // Future: navigate to other sections based on tabId
  };

  return (
    <div className="w-full h-full bg-white border-r border-gray-200">
      {/* ===== MOBILE LAYOUT ===== */}
      <div
        className="md:hidden flex flex-col w-full bg-white"
        style={{
          position: 'fixed',
          inset: 0,
          height: '100dvh',
          maxHeight: '100dvh',
          overflow: 'hidden',
          overscrollBehavior: 'none',
          touchAction: 'none',
          backgroundColor: '#ffffff',
        }}
      >
        {/* Header - FIXED TOP with safe area */}
        <div
          className="flex items-center justify-between px-6 border-b border-gray-200 bg-white"
          style={{
            height: 'calc(56px + env(safe-area-inset-top))',
            paddingTop: 'env(safe-area-inset-top)',
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            zIndex: 50,
            backgroundColor: '#ffffff',
          }}
        >
          <h1 className="text-xl font-medium text-gray-900">milkyhoop</h1>
          <button className="p-2 hover:bg-gray-100 rounded-lg">
            <svg className="w-6 h-6 text-gray-700" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
            </svg>
          </button>
        </div>

        {/* Content - ABSOLUTE positioned between header and footer */}
        <div
          className="bg-white"
          style={{
            position: 'absolute',
            top: 'calc(56px + env(safe-area-inset-top))',
            bottom: 'calc(56px + env(safe-area-inset-bottom))',
            left: 0,
            right: 0,
            overflowY: 'scroll',
            overflowX: 'hidden',
            overscrollBehavior: 'contain',
            touchAction: 'pan-y',
            WebkitOverflowScrolling: 'touch',
            backgroundColor: '#ffffff',
          }}
        >
          {/* Install PWA Banner */}
          <InstallPrompt />

          {/* Search Bar */}
          <div className="px-6 py-3 bg-white">
            <div className="relative">
              <input
                type="text"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                placeholder="Search tenants..."
                className="w-full pl-9 pr-3 py-2 text-sm bg-gray-100 rounded-lg focus:outline-none focus:bg-gray-200 transition-colors"
              />
              <svg className="absolute left-3 top-2.5 w-4 h-4 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
            </div>
          </div>

          {/* Tenant List */}
          {/* Milky Assistant */}
          <div
            onClick={() => onSelectTenant('milky-assistant')}
            className={`px-6 py-4 cursor-pointer transition-colors ${
              activeTenant?.id === 'milky-assistant' ? 'bg-gray-100' : 'hover:bg-gray-50'
            }`}
          >
            <div className="flex items-start gap-3">
              <div className="w-12 h-12 rounded-full bg-gray-200 flex items-center justify-center flex-shrink-0">
                <span className="text-xl"></span>
              </div>
              <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between mb-1">
                  <h3 className="text-sm font-medium text-gray-900">Milky Assistant</h3>
                  <span className="text-xs text-gray-500">Now</span>
                </div>
                <p className="text-xs text-gray-600 truncate">Selamat datang! Mau buat tenant?</p>
              </div>
            </div>
          </div>

          {/* Loading State */}
          {isLoading && (
            <div className="px-6 py-4 text-center text-gray-500 text-sm">
              Loading tenants...
            </div>
          )}

          {/* Tenants */}
          {filteredTenants.map((tenant: Tenant) => (
            <div
              key={tenant.id}
              onClick={() => onSelectTenant(tenant.id)}
              className={`px-6 py-4 cursor-pointer transition-colors ${
                activeTenant?.id === tenant.id ? 'bg-gray-100' : 'hover:bg-gray-50'
              }`}
            >
              <div className="flex items-start gap-3">
                <div className="w-12 h-12 rounded-full bg-gray-200 flex items-center justify-center text-gray-700 font-medium flex-shrink-0 text-base">
                  {tenant.avatar || tenant.name.charAt(0)}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between mb-1">
                    <div className="flex items-center gap-1.5">
                      <h3 className="text-sm font-medium text-gray-900 truncate">{tenant.name}</h3>
                      {tenant.isPrivate && (
                        <svg className="w-3.5 h-3.5 text-gray-500 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                        </svg>
                      )}
                    </div>
                    <span className="text-xs text-gray-500 flex-shrink-0">{tenant.lastMessageTime || 'Now'}</span>
                  </div>
                  <div className="flex items-center justify-between gap-2">
                    <p className="text-xs text-gray-500 truncate flex-1">{tenant.lastMessage}</p>
                    {tenant.unreadCount && (
                      <span className="px-1.5 py-0.5 text-xs font-medium bg-gray-900 text-white rounded-full flex-shrink-0">
                        {tenant.unreadCount}
                      </span>
                    )}
                  </div>
                  {tenant.role && (
                    <p className="text-xs text-gray-500 mt-0.5">({tenant.role})</p>
                  )}
                </div>
              </div>
            </div>
          ))}

          {/* Empty State */}
          {filteredTenants.length === 0 && searchQuery && (
            <div className="px-4 py-8 text-center text-gray-500 text-sm">
              No tenants found for "{searchQuery}"
            </div>
          )}
        </div>

        {/* Bottom Navigation - FIXED BOTTOM */}
        <div
          style={{
            position: 'fixed',
            bottom: 0,
            left: 0,
            right: 0,
            zIndex: 50,
            backgroundColor: '#ffffff',
            paddingBottom: 'env(safe-area-inset-bottom)',
          }}
        >
          <BottomNavigation
            activeTab={activeNavTab}
            onTabChange={handleNavTabChange}
          />
        </div>
      </div>

      {/* ===== DESKTOP LAYOUT ===== */}
      <div className="hidden md:flex md:flex-col md:h-full">
        {/* Header Desktop */}
        <div className="px-4 py-4 border-b border-gray-200">
          <h1 className="text-2xl font-semibold text-gray-900 mb-4">MilkyHoop</h1>

          {/* Search Box */}
          <div className="relative mb-3">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="Cari atau mulai chat baru"
              className="w-full pl-10 pr-3 py-2.5 text-sm bg-gray-100 rounded-lg focus:outline-none focus:bg-gray-200 transition-colors"
            />
            <svg className="absolute left-3 top-3 w-4 h-4 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
            </svg>
          </div>

          {/* Navigation Tabs */}
          <div className="flex gap-2">
            <button className="px-3 py-1.5 text-sm font-medium bg-gray-900 text-white rounded-full">
              Semua
            </button>
            <button className="px-3 py-1.5 text-sm font-medium text-gray-700 hover:bg-gray-100 rounded-full transition-colors">
              Belum dibaca
            </button>
          </div>
        </div>

        {/* Tenant List */}
        <div className="flex-1 overflow-y-auto">
          {/* Milky Assistant */}
          <div
            onClick={() => onSelectTenant('milky-assistant')}
            className={`px-6 py-4 cursor-pointer transition-colors ${
              activeTenant?.id === 'milky-assistant' ? 'bg-gray-100' : 'hover:bg-gray-50'
            }`}
          >
            <div className="flex items-start gap-3">
              <div className="w-12 h-12 rounded-full bg-gray-200 flex items-center justify-center flex-shrink-0">
                <span className="text-xl"></span>
              </div>
              <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between mb-1">
                  <h3 className="text-sm font-medium text-gray-900">Milky Assistant</h3>
                  <span className="text-xs text-gray-500">Now</span>
                </div>
                <p className="text-xs text-gray-600 truncate">Selamat datang! Mau buat tenant?</p>
              </div>
            </div>
          </div>

          {/* Loading State */}
          {isLoading && (
            <div className="px-6 py-4 text-center text-gray-500 text-sm">
              Loading tenants...
            </div>
          )}

          {/* Tenants */}
          {filteredTenants.map((tenant: Tenant) => (
            <div
              key={tenant.id}
              onClick={() => onSelectTenant(tenant.id)}
              className={`px-6 py-4 cursor-pointer transition-colors ${
                activeTenant?.id === tenant.id ? 'bg-gray-100' : 'hover:bg-gray-50'
              }`}
            >
              <div className="flex items-start gap-3">
                <div className="w-12 h-12 rounded-full bg-gray-200 flex items-center justify-center text-gray-700 font-medium flex-shrink-0 text-base">
                  {tenant.avatar || tenant.name.charAt(0)}
                </div>
                <div className="flex-1 min-w-0">
                  <div className="flex items-center justify-between mb-1">
                    <div className="flex items-center gap-1.5">
                      <h3 className="text-sm font-medium text-gray-900 truncate">{tenant.name}</h3>
                      {tenant.isPrivate && (
                        <svg className="w-3.5 h-3.5 text-gray-500 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                        </svg>
                      )}
                    </div>
                    <span className="text-xs text-gray-500 flex-shrink-0">{tenant.lastMessageTime || 'Now'}</span>
                  </div>
                  <div className="flex items-center justify-between gap-2">
                    <p className="text-xs text-gray-500 truncate flex-1">{tenant.lastMessage}</p>
                    {tenant.unreadCount && (
                      <span className="px-1.5 py-0.5 text-xs font-medium bg-gray-900 text-white rounded-full flex-shrink-0">
                        {tenant.unreadCount}
                      </span>
                    )}
                  </div>
                  {tenant.role && (
                    <p className="text-xs text-gray-500 mt-0.5">({tenant.role})</p>
                  )}
                </div>
              </div>
            </div>
          ))}

          {/* Empty State */}
          {filteredTenants.length === 0 && searchQuery && (
            <div className="px-4 py-8 text-center text-gray-500 text-sm">
              No tenants found for "{searchQuery}"
            </div>
          )}
        </div>

        {/* Create Tenant Button */}
        <div className="p-4">
          <button className="w-full px-4 py-2.5 bg-gray-900 hover:bg-black text-white text-sm font-medium rounded-lg transition-colors flex items-center justify-center gap-2">
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            Buat Tenant Baru
          </button>
        </div>
      </div>
    </div>
  );
};

export default TenantSidebar;