import React, { useState, useEffect, useRef, useCallback } from 'react';
import BarcodeScanner, { ScanResult } from '../utils/BarcodeScanner';

// Animated scanning dots component
const ScanningDots: React.FC = () => {
  const [dots, setDots] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setDots(prev => (prev + 1) % 4);
    }, 400);
    return () => clearInterval(interval);
  }, []);

  return <span>Scanning{'.'.repeat(dots)}</span>;
};

interface BarcodeRegistrationModalProps {
  isOpen: boolean;
  productId: string;
  productName: string;
  onClose: () => void;
  onSuccess: (barcode: string) => void;
}

type ModalMode = 'choose' | 'scan' | 'manual';

const BarcodeRegistrationModal: React.FC<BarcodeRegistrationModalProps> = ({
  isOpen,
  productId,
  productName,
  onClose,
  onSuccess,
}) => {
  const [mode, setMode] = useState<ModalMode>('choose');
  const [barcode, setBarcode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [isScanning, setIsScanning] = useState(false);
  const [cameraError, setCameraError] = useState('');
  const [scannedCode, setScannedCode] = useState<string | null>(null);
  const [validationError, setValidationError] = useState('');
  const [isScannerLoading, setIsScannerLoading] = useState(false);

  const isActiveRef = useRef(true);
  const hasDetectedRef = useRef(false);
  const scannerRef = useRef<BarcodeScanner | null>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  // Reset state when modal opens/closes
  useEffect(() => {
    if (isOpen) {
      setMode('choose');
      setBarcode('');
      setError('');
      setCameraError('');
      setIsScanning(false);
      setScannedCode(null);
      setValidationError('');
      setIsScannerLoading(false);
      isActiveRef.current = true;
      hasDetectedRef.current = false;
    } else {
      cleanupScanner();
      isActiveRef.current = false;
    }
  }, [isOpen]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      isActiveRef.current = false;
      cleanupScanner();
    };
  }, []);

  const cleanupScanner = () => {
    if (scannerRef.current) {
      scannerRef.current.stop();
      scannerRef.current = null;
    }
    console.log('[BarcodeModal] Scanner cleanup');
  };

  // Validate barcode: must be exactly 13 digits (EAN-13)
  const validateBarcode = (code: string): { valid: boolean; message: string } => {
    if (!code) {
      return { valid: false, message: 'Barcode kosong' };
    }

    if (!/^\d+$/.test(code)) {
      return { valid: false, message: 'Barcode harus berupa angka saja' };
    }

    if (code.length !== 13) {
      return {
        valid: false,
        message: `Barcode harus 13 digit (EAN-13). Terdeteksi ${code.length} digit.`
      };
    }

    return { valid: true, message: '' };
  };

  // Handle submit barcode
  const handleSubmitBarcode = useCallback(async (code: string) => {
    const validation = validateBarcode(code);
    if (!validation.valid) {
      setError(validation.message);
      return;
    }

    setIsLoading(true);
    setError('');

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/${productId}/barcode`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ barcode: code }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Gagal menyimpan barcode');
      }

      onSuccess(code);
    } catch (err: any) {
      console.error('[BarcodeModal] Submit error:', err);
      setError(err.message || 'Gagal menyimpan barcode');
    } finally {
      setIsLoading(false);
    }
  }, [productId, onSuccess]);

  // Handle scan success
  const onScanSuccess = useCallback((result: ScanResult) => {
    if (!isActiveRef.current || hasDetectedRef.current) return;

    const code = result.text;
    console.log('[BarcodeModal] Barcode detected:', code, 'format:', result.format);

    hasDetectedRef.current = true;
    cleanupScanner();
    setIsScanning(false);

    // Haptic feedback
    if (navigator.vibrate) {
      navigator.vibrate(200);
    }

    const validation = validateBarcode(code);
    setScannedCode(code);

    if (!validation.valid) {
      setValidationError(validation.message);
    } else {
      handleSubmitBarcode(code);
    }
  }, [handleSubmitBarcode]);

  // Initialize scanner
  const initializeScanner = useCallback(async () => {
    setCameraError('');
    setScannedCode(null);
    setValidationError('');
    setIsScannerLoading(true);
    hasDetectedRef.current = false;

    console.log('[BarcodeModal] Initializing scanner...');

    try {
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        setCameraError('Browser tidak mendukung akses kamera.');
        setIsScannerLoading(false);
        return;
      }

      await new Promise(resolve => setTimeout(resolve, 100));

      if (!isActiveRef.current || !videoRef.current) return;

      const scanner = new BarcodeScanner(videoRef.current, {
        desiredWidth: 1280,
        desiredHeight: 720,
        decodeFPS: 12,
        roiResizeLongSide: 800,
        consensusFrames: 2,
      });

      scanner.onFound(onScanSuccess);

      scanner.onProgress((state) => {
        console.log('[BarcodeModal] Progress:', state.phase);
        if (state.phase === 'camera-ready') {
          setIsScanning(true);
          setIsScannerLoading(false);
        }
      });

      scanner.onError((err) => {
        console.error('[BarcodeModal] Scanner error:', err);
        if (err.message.includes('Permission') || err.name === 'NotAllowedError') {
          setCameraError('Akses kamera ditolak. Izinkan akses kamera di pengaturan browser.');
        } else if (err.name === 'NotFoundError') {
          setCameraError('Kamera tidak ditemukan.');
        } else {
          setCameraError(`Gagal mengakses kamera: ${err.message}`);
        }
        setIsScannerLoading(false);
      });

      scannerRef.current = scanner;
      await scanner.start();

    } catch (err: any) {
      console.error('[BarcodeModal] Scanner init error:', err);
      setCameraError('Gagal memulai scanner.');
      setIsScannerLoading(false);
    }
  }, [onScanSuccess]);

  const handleModeSelect = async (selectedMode: 'scan' | 'manual') => {
    setMode(selectedMode);
    setError('');
    setValidationError('');
    setScannedCode(null);

    if (selectedMode === 'scan') {
      await initializeScanner();
    }
  };

  const handleBack = () => {
    cleanupScanner();
    setIsScanning(false);
    setCameraError('');
    setMode('choose');
    setBarcode('');
    setError('');
    setScannedCode(null);
    setValidationError('');
  };

  const handleRescan = async () => {
    setScannedCode(null);
    setValidationError('');
    hasDetectedRef.current = false;
    await initializeScanner();
  };

  const handleSwitchToManual = () => {
    setMode('manual');
    setScannedCode(null);
    setValidationError('');
    if (scannedCode) {
      setBarcode(scannedCode);
    }
  };

  // Auto-submit when 13 digits entered in manual mode
  useEffect(() => {
    if (mode === 'manual' && barcode.length === 13) {
      handleSubmitBarcode(barcode.trim());
    }
  }, [barcode, mode, handleSubmitBarcode]);

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget && mode !== 'scan') {
      onClose();
    }
  };

  // Clean neutral border - no traffic light colors
  const getBorderColor = () => 'border-white/70';
  const getGlowColor = () => 'rgba(255, 255, 255, 0.3)';

  if (!isOpen) return null;

  // FULL SCREEN SCAN MODE
  if (mode === 'scan') {
    return (
      <div className="fixed inset-0 z-[200] bg-gray-900 flex flex-col">
        {/* Header */}
        <div className="bg-white border-b border-gray-200 p-4 flex items-center gap-3">
          <button
            onClick={handleBack}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors active:bg-gray-200"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </button>
          <div className="flex-1">
            <h1 className="font-semibold text-lg">Daftarkan Barcode</h1>
            <p className="text-sm text-gray-500 truncate">{productName}</p>
          </div>
          <button
            onClick={onClose}
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        {/* Scanner View */}
        <div className="flex-1 relative bg-black">
          {/* Video Element */}
          <video
            ref={videoRef}
            className="absolute inset-0 w-full h-full object-cover"
            playsInline
            muted
            autoPlay
          />

          {/* Camera Error */}
          {cameraError && (
            <div className="absolute inset-0 flex items-center justify-center text-white p-6 z-10 bg-black">
              <div className="text-center max-w-md">
                <svg className="w-12 h-12 mx-auto mb-3 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
                <p className="mb-4">{cameraError}</p>
                <button
                  onClick={initializeScanner}
                  className="px-6 py-3 bg-amber-500 text-white rounded-lg hover:bg-amber-600 active:bg-amber-700 transition-colors flex items-center gap-2 mx-auto"
                >
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                  </svg>
                  Coba Lagi
                </button>
              </div>
            </div>
          )}

          {/* Loading State */}
          {isScannerLoading && (
            <div className="absolute inset-0 flex items-center justify-center text-white z-10 bg-black">
              <div className="text-center">
                <svg className="w-12 h-12 mx-auto mb-3 animate-pulse" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                <p>Memulai scanner...</p>
              </div>
            </div>
          )}

          {/* Scan overlay with targeting box */}
          {isScanning && !scannedCode && (
            <div className="absolute inset-0 pointer-events-none z-10">
              <div className="absolute inset-0 flex items-center justify-center">
                <div
                  className="relative transition-all duration-200"
                  style={{
                    width: '85vw',
                    height: '60vh',
                    maxWidth: '500px',
                    maxHeight: '400px',
                    boxShadow: `0 0 20px ${getGlowColor()}`
                  }}
                >
                  {/* Corner brackets */}
                  <div className={`absolute top-0 left-0 w-10 h-10 border-t-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute top-0 right-0 w-10 h-10 border-t-4 border-r-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute bottom-0 left-0 w-10 h-10 border-b-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                  <div className={`absolute bottom-0 right-0 w-10 h-10 border-b-4 border-r-4 ${getBorderColor()} transition-colors`}></div>

                  {/* Scan line animation */}
                  <div
                    className="absolute left-2 right-2 h-0.5 bg-white/80"
                    style={{
                      boxShadow: '0 0 10px rgba(255, 255, 255, 0.6)',
                      animation: 'scanLine 1.5s ease-in-out infinite'
                    }}
                  />
                </div>
              </div>
            </div>
          )}

          {/* Invalid Barcode Modal */}
          {scannedCode && validationError && (
            <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
              <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                  <p className="text-sm font-medium text-gray-700 mb-1">Barcode Tidak Valid</p>
                  <p className="text-xs text-gray-500 mb-2">{validationError}</p>
                  <p className="text-xs text-gray-400 mb-4 font-mono">{scannedCode}</p>
                  <div className="flex gap-2">
                    <button
                      onClick={handleRescan}
                      className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50"
                    >
                      Scan Ulang
                    </button>
                    <button
                      onClick={handleSwitchToManual}
                      className="flex-1 px-4 py-2 bg-gray-800 text-white rounded-lg text-sm font-medium hover:bg-gray-700"
                    >
                      Input Manual
                    </button>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Saving overlay */}
          {isLoading && (
            <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
              <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
                <div className="text-center">
                  <svg className="animate-spin w-8 h-8 mx-auto mb-3 text-gray-600" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  <p className="text-sm text-gray-600">Menyimpan barcode...</p>
                </div>
              </div>
            </div>
          )}

          {/* CSS for scan line animation */}
          <style>{`
            @keyframes scanLine {
              0% { top: 10%; }
              50% { top: 90%; }
              100% { top: 10%; }
            }
          `}</style>

          {/* Scanning Status - Clean minimal indicator */}
          {isScanning && !scannedCode && (
            <div className="absolute bottom-32 left-0 right-0 text-center text-white z-20">
              <div className="inline-block px-6 py-3 rounded-full backdrop-blur-sm bg-black/60">
                <p className="text-sm font-medium">
                  <ScanningDots />
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }

  // CHOOSE MODE or MANUAL MODE
  return (
    <div
      className="fixed inset-0 z-50 bg-black/50 flex items-center justify-center p-4"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-xl w-full max-w-md shadow-2xl overflow-hidden">
        {/* Header */}
        <div className="p-4 border-b border-gray-200">
          <div className="flex items-center justify-between">
            <h2 className="text-lg font-semibold text-gray-900">
              {mode === 'choose' ? 'Daftarkan Barcode' : 'Input Barcode Manual'}
            </h2>
            <button
              onClick={onClose}
              className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <svg className="w-5 h-5 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          <p className="text-sm text-gray-500 mt-1 truncate">{productName}</p>
        </div>

        {/* Content */}
        <div className="p-4">
          {mode === 'choose' ? (
            <div className="space-y-3">
              <button
                onClick={() => handleModeSelect('scan')}
                className="w-full p-4 border-2 border-gray-200 rounded-xl hover:border-amber-500 hover:bg-amber-50 transition-colors flex items-center gap-4"
              >
                <div className="w-12 h-12 bg-amber-100 rounded-full flex items-center justify-center">
                  <svg className="w-6 h-6 text-amber-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
                  </svg>
                </div>
                <div className="text-left">
                  <p className="font-medium text-gray-900">Scan Barcode</p>
                  <p className="text-sm text-gray-500">Gunakan kamera untuk scan</p>
                </div>
              </button>

              <button
                onClick={() => handleModeSelect('manual')}
                className="w-full p-4 border-2 border-gray-200 rounded-xl hover:border-gray-400 hover:bg-gray-50 transition-colors flex items-center gap-4"
              >
                <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
                  <svg className="w-6 h-6 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                  </svg>
                </div>
                <div className="text-left">
                  <p className="font-medium text-gray-900">Input Manual</p>
                  <p className="text-sm text-gray-500">Ketik barcode 13 digit</p>
                </div>
              </button>
            </div>
          ) : (
            <div className="space-y-4">
              <input
                type="text"
                value={barcode}
                onChange={(e) => {
                  const val = e.target.value.replace(/\D/g, '').slice(0, 13);
                  setBarcode(val);
                  setError('');
                }}
                placeholder="13 digit code"
                className="w-full px-4 py-4 text-xl text-center font-mono border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-amber-500 tracking-widest placeholder:text-gray-400 placeholder:text-base placeholder:font-sans"
                inputMode="numeric"
                pattern="[0-9]*"
                maxLength={13}
                autoFocus
              />

              {error && (
                <div className="p-3 bg-red-50 border border-red-200 rounded-lg">
                  <p className="text-sm text-red-600">{error}</p>
                </div>
              )}

              {isLoading && (
                <div className="text-center py-2">
                  <p className="text-sm text-gray-500">Menyimpan...</p>
                </div>
              )}

              <button
                type="button"
                onClick={handleBack}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg text-gray-700 font-medium hover:bg-gray-50 transition-colors"
              >
                Kembali
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default BarcodeRegistrationModal;
