import React, { useState, useEffect, useMemo } from 'react';
import Fuse from 'fuse.js';

interface Product {
  name: string;
  unit: string;
  last_price: number | null;
}

interface ProductAutocompleteProps {
  value: string;
  onChange: (value: string, product?: Product) => void;
  placeholder?: string;
  className?: string;
  disabled?: boolean;
}

export const ProductAutocomplete: React.FC<ProductAutocompleteProps> = ({
  value,
  onChange,
  placeholder = "Ketik nama produk...",
  className = "",
  disabled = false
}) => {
  const [allProducts, setAllProducts] = useState<Product[]>([]);
  const [isInitialLoading, setIsInitialLoading] = useState(true);
  const [selectedIndex, setSelectedIndex] = useState(-1);
  const [fuse, setFuse] = useState<Fuse<Product> | null>(null);

  // Prefetch ALL products on mount (one-time)
  useEffect(() => {
    const fetchAllProducts = async () => {
      setIsInitialLoading(true);
      try {
        const token = localStorage.getItem('access_token');
        const response = await fetch('/api/products/all', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!response.ok) throw new Error('Failed to fetch products');

        const data = await response.json();
        setAllProducts(data);

        // Initialize fuzzy search
        const fuseInstance = new Fuse<Product>(data, {
          keys: ['name'],
          threshold: 0.3,
          distance: 100,
          minMatchCharLength: 1
        });
        setFuse(fuseInstance);
      } catch (error) {
        console.error('Failed to fetch products:', error);
      } finally {
        setIsInitialLoading(false);
      }
    };

    fetchAllProducts();
  }, []);

  // INSTANT client-side filtering (no API call!)
  const filteredProducts = useMemo(() => {
    if (!value || !fuse) {
      // Show top 10 if no query
      return allProducts.slice(0, 10);
    }

    // Fuzzy search
    const results = fuse.search(value);
    return results.map(r => r.item).slice(0, 10);
  }, [value, fuse, allProducts]);

  const handleSelect = (product: Product) => {
    onChange(product.name, product);
  };

  const formatPrice = (price: number | null) => {
    if (!price) return '';
    if (price >= 1000000) return `${(price / 1000000).toFixed(1)}jt`;
    if (price >= 1000) return `${Math.round(price / 1000)}rb`;
    return price.toString();
  };

  // Initial loading state (only shown once on mount)
  if (isInitialLoading) {
    return (
      <div className="p-4 text-center text-gray-500">
        <svg className="animate-spin h-5 w-5 mx-auto text-amber-500" fill="none" viewBox="0 0 24 24">
          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
        </svg>
        <div className="mt-2 text-sm">Loading produk...</div>
      </div>
    );
  }

  // No results
  if (filteredProducts.length === 0) {
    return (
      <div className="p-4 text-center text-gray-500">
        <svg className="w-8 h-8 mx-auto mb-2 opacity-30" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
        </svg>
        <div className="text-sm">{value ? 'Produk tidak ditemukan' : 'Belum ada produk'}</div>
        {value && (
          <button
            onClick={() => onChange(value)}
            className="mt-2 text-xs text-amber-600 hover:text-amber-700"
          >
            Tambah "{value}" sebagai produk baru
          </button>
        )}
      </div>
    );
  }

  // Show suggestions - INSTANT (no loading spinner during filtering)
  return (
    <div className="p-2 max-h-80 overflow-y-auto">
      <div className="text-xs text-gray-500 mb-2 px-2">
        {value ? ` ${filteredProducts.length} hasil:` : ' Produk Sering Dibeli:'}
      </div>
      <div className="space-y-0.5">
        {filteredProducts.map((product, index) => (
          <button
            key={`${product.name}-${product.unit}-${index}`}
            onClick={() => handleSelect(product)}
            className={`w-full px-3 py-2 text-left hover:bg-amber-50 flex items-center justify-between rounded transition-colors ${
              index === selectedIndex ? 'bg-amber-50' : ''
            }`}
          >
            <div className="flex items-center gap-2">
              <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
              <div>
                <div className="font-medium text-sm">{product.name}</div>
                <div className="text-xs text-gray-500">
                  {product.unit}
                </div>
              </div>
            </div>
            {product.last_price && (
              <span className="text-xs text-gray-500">
                {formatPrice(product.last_price)}
              </span>
            )}
          </button>
        ))}
      </div>
    </div>
  );
};

export default ProductAutocomplete;
