import React, { useState, useEffect, useRef, useCallback } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import BarcodeScanner, { ScanResult } from '../utils/BarcodeScanner';

// Animated scanning dots component
const ScanningDots: React.FC = () => {
  const [dots, setDots] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setDots(prev => (prev + 1) % 4);
    }, 400);
    return () => clearInterval(interval);
  }, []);

  return <span>Scanning{'.'.repeat(dots)}</span>;
};


const SalesScanPage: React.FC = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const isActiveRef = useRef(true);
  const hasDetectedRef = useRef(false);
  const scannerRef = useRef<BarcodeScanner | null>(null);
  const videoRef = useRef<HTMLVideoElement>(null);

  // Get context and tenantId from route state or URL params
  const routeState = location.state as any;
  const params = new URLSearchParams(location.search);
  const context = routeState?.context || params.get('context') || 'sale';
  const tenantId = routeState?.tenantId || params.get('tenant');
  const returnTo = routeState?.returnTo || `/app?tenant=${tenantId}&action=openPurchaseForm`;

  const [hasPermission, setHasPermission] = useState<boolean | null>(null);
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(true);
  const [isScanning, setIsScanning] = useState(false);
  const [scannedCode, setScannedCode] = useState<string | null>(null);
  const [isLookingUp, setIsLookingUp] = useState(false);
  const [lookupError, setLookupError] = useState<string>('');
  const [showSuccess, setShowSuccess] = useState(false);
  const [showManualInput, setShowManualInput] = useState(false);
  const [manualBarcode, setManualBarcode] = useState('');
  const [manualInputMode, setManualInputMode] = useState<'barcode' | 'name'>('barcode');
  const [productNameInput, setProductNameInput] = useState('');

  // Handle successful barcode detection
  const onScanSuccess = useCallback(async (result: ScanResult) => {
    if (!isActiveRef.current || hasDetectedRef.current || scannedCode) return;

    const decodedText = result.text;
    console.log('[Scanner] Barcode detected:', decodedText, 'format:', result.format);

    hasDetectedRef.current = true;
    setScannedCode(decodedText);
    setIsScanning(false);

    // Stop scanner
    if (scannerRef.current) {
      scannerRef.current.stop();
    }

    // Haptic feedback
    if (navigator.vibrate) {
      navigator.vibrate(200);
    }

    // Lookup product
    await lookupProduct(decodedText);
  }, [scannedCode]);

  // Initialize scanner
  const initializeScanner = useCallback(async () => {
    console.log('[Scanner] Initializing BarcodeScanner...');
    setIsLoading(true);
    setError('');

    try {
      // Check camera support
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        setError('Browser tidak mendukung akses kamera.');
        setHasPermission(false);
        setIsLoading(false);
        return;
      }

      // Wait for video element
      await new Promise(resolve => setTimeout(resolve, 100));

      if (!isActiveRef.current || !videoRef.current) return;

      // Create scanner instance
      const scanner = new BarcodeScanner(videoRef.current, {
        desiredWidth: 1280,
        desiredHeight: 720,
        decodeFPS: 12,
        roiResizeLongSide: 800,
        consensusFrames: 2,
      });

      // Set up callbacks
      scanner.onFound(onScanSuccess);

      scanner.onProgress((state) => {
        console.log('[Scanner] Progress:', state.phase);
        if (state.phase === 'camera-ready') {
          setHasPermission(true);
          setIsScanning(true);
          setIsLoading(false);
        }
      });

      scanner.onError((err) => {
        console.error('[Scanner] Error:', err);
        if (err.message.includes('Permission') || err.name === 'NotAllowedError') {
          setError('Akses kamera ditolak. Mohon izinkan akses kamera di pengaturan browser.');
        } else if (err.name === 'NotFoundError') {
          setError('Kamera tidak ditemukan. Pastikan perangkat memiliki kamera.');
        } else {
          setError(`Gagal mengakses kamera: ${err.message}`);
        }
        setHasPermission(false);
        setIsLoading(false);
      });

      // Store reference and start
      scannerRef.current = scanner;
      await scanner.start();

    } catch (err: any) {
      console.error('[Scanner] Initialization error:', err);

      if (!isActiveRef.current) return;

      setHasPermission(false);
      setIsLoading(false);

      if (err.name === 'NotAllowedError') {
        setError('Akses kamera ditolak. Mohon izinkan akses kamera di pengaturan browser.');
      } else if (err.name === 'NotFoundError') {
        setError('Kamera tidak ditemukan. Pastikan perangkat memiliki kamera.');
      } else {
        setError(`Gagal mengakses kamera: ${err.message || 'Unknown error'}`);
      }
    }
  }, [onScanSuccess]);

  // Cleanup scanner
  const cleanupScanner = useCallback(() => {
    if (scannerRef.current) {
      scannerRef.current.stop();
      scannerRef.current = null;
    }
    console.log('[Scanner] Cleanup completed');
  }, []);

  // Initialize on mount
  useEffect(() => {
    isActiveRef.current = true;
    hasDetectedRef.current = false;
    initializeScanner();

    return () => {
      isActiveRef.current = false;
      cleanupScanner();
    };
  }, [initializeScanner, cleanupScanner]);

  // Product lookup
  const lookupProduct = async (barcode: string) => {
    setIsLookingUp(true);
    setLookupError('');

    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/barcode/${barcode}?tenant_id=${tenantId}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const product = await response.json();
        console.log('[Scanner] Product found:', product);

        setShowSuccess(true);
        setIsLookingUp(false);

        await new Promise(resolve => setTimeout(resolve, 800));

        if (!isActiveRef.current) return;

        navigate(returnTo, {
          state: {
            action: 'purchase',
            productData: {
              name: product.nama_produk || product.name,
              barcode: barcode,
              unit: product.satuan || product.unit,
              // Auto-fill fields from last transaction
              units_per_pack: product.units_per_pack,
              harga_jual: product.harga_jual,
              last_price: product.last_price,
            }
          },
          replace: true
        });
      } else if (response.status === 404) {
        setLookupError(`Produk tidak ditemukan untuk barcode: ${barcode}`);
        setIsLookingUp(false);
      } else {
        setLookupError('Gagal mencari produk. Silakan coba lagi.');
        setIsLookingUp(false);
      }
    } catch (err) {
      console.error('[Scanner] Product lookup error:', err);
      setLookupError('Gagal mencari produk. Silakan coba lagi.');
      setIsLookingUp(false);
    }
  };

  // Rescan handler
  const handleRescan = async () => {
    setScannedCode(null);
    setLookupError('');
    setShowSuccess(false);
    hasDetectedRef.current = false;

    await initializeScanner();
  };

  // Manual input handler - show popup
  const handleManualInput = () => {
    setShowManualInput(true);
    setManualBarcode('');
  };

  // Auto-submit when 13 digits entered
  useEffect(() => {
    if (manualBarcode.length === 13 && showManualInput) {
      setShowManualInput(false);
      hasDetectedRef.current = true;
      setScannedCode(manualBarcode);
      setIsScanning(false);
      if (scannerRef.current) {
        scannerRef.current.stop();
      }
      lookupProduct(manualBarcode);
    }
  }, [manualBarcode, showManualInput]);

  // Back handler
  const handleBack = () => {
    if (context === 'purchase' && tenantId) {
      navigate(`/app?tenant=${tenantId}&action=openPurchaseForm`, { replace: true });
    } else if (tenantId) {
      navigate(`/app?tenant=${tenantId}`, { replace: true });
    } else {
      navigate(-1);
    }
  };

  // Clean neutral border - no traffic light colors
  const getBorderColor = () => 'border-white/70';
  const getGlowColor = () => 'rgba(255, 255, 255, 0.3)';

  return (
    <div className="fixed inset-0 bg-gray-900 flex flex-col">
      {/* Header - sticky at top */}
      <div className="bg-white border-b border-gray-200 p-4 flex items-center gap-3 flex-shrink-0">
        <button
          onClick={handleBack}
          className="p-2 hover:bg-gray-100 rounded-lg transition-colors active:bg-gray-200"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
        </button>
        <div className="flex-1">
          <h1 className="font-semibold text-lg">
            {context === 'purchase' ? 'Scan Produk Pembelian' : 'Scan Produk'}
          </h1>
        </div>
        {/* Manual Input Button - Prominent */}
        <button
          onClick={handleManualInput}
          className="px-4 py-2.5 bg-gray-800 text-white text-sm font-medium rounded-lg hover:bg-gray-700 active:bg-gray-900 transition-colors"
        >
          Input Manual
        </button>
      </div>

      {/* Manual Input Popup - With Tab for Barcode/Name */}
      {showManualInput && (
        <div
          className="fixed inset-0 z-[100] bg-black/60 flex items-center justify-center p-4"
          onClick={() => setShowManualInput(false)}
        >
          <div
            className="bg-white rounded-xl w-full max-w-sm shadow-2xl p-4"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Tab Toggle */}
            <div className="flex gap-2 mb-4">
              <button
                onClick={() => setManualInputMode('barcode')}
                className={`flex-1 py-2.5 rounded-lg font-medium text-sm transition-colors ${
                  manualInputMode === 'barcode'
                    ? 'bg-gray-800 text-white'
                    : 'bg-gray-100 text-gray-600'
                }`}
              >
                 Barcode
              </button>
              <button
                onClick={() => setManualInputMode('name')}
                className={`flex-1 py-2.5 rounded-lg font-medium text-sm transition-colors ${
                  manualInputMode === 'name'
                    ? 'bg-gray-800 text-white'
                    : 'bg-gray-100 text-gray-600'
                }`}
              >
                 Nama Produk
              </button>
            </div>

            {/* Barcode Input */}
            {manualInputMode === 'barcode' && (
              <input
                type="text"
                inputMode="numeric"
                pattern="[0-9]*"
                value={manualBarcode}
                onChange={(e) => setManualBarcode(e.target.value.replace(/\D/g, '').slice(0, 13))}
                placeholder="13 digit code"
                className="w-full px-4 py-4 text-xl text-center font-mono border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-amber-500 tracking-widest placeholder:text-gray-400 placeholder:text-base placeholder:font-sans"
                autoFocus
                maxLength={13}
              />
            )}

            {/* Product Name Input */}
            {manualInputMode === 'name' && (
              <div>
                <input
                  type="text"
                  value={productNameInput}
                  onChange={(e) => setProductNameInput(e.target.value)}
                  placeholder="Ketik nama produk..."
                  className="w-full px-4 py-4 text-lg border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-amber-500 placeholder:text-gray-400"
                  autoFocus
                />
                {productNameInput.length >= 2 && (
                  <button
                    onClick={() => {
                      // Navigate back with product name (no barcode)
                      setShowManualInput(false);
                      navigate(returnTo, {
                        state: {
                          action: 'purchase',
                          productData: {
                            name: productNameInput,
                            barcode: '',
                            unit: undefined,
                          }
                        },
                        replace: true
                      });
                    }}
                    className="w-full mt-3 py-3 bg-green-600 text-white rounded-lg font-medium hover:bg-green-700 active:bg-green-800 transition-colors"
                  >
                    Gunakan "{productNameInput}"
                  </button>
                )}
              </div>
            )}
          </div>
        </div>
      )}

      {/* Scanner View */}
      <div className="flex-1 relative bg-black">
        {/* Video Element */}
        <video
          ref={videoRef}
          className="absolute inset-0 w-full h-full object-cover"
          playsInline
          muted
          autoPlay
        />

        {/* Scan overlay with targeting box */}
        {isScanning && !scannedCode && (
          <div className="absolute inset-0 pointer-events-none z-10">
            <div className="absolute inset-0 flex items-center justify-center">
              <div
                className="relative transition-all duration-200"
                style={{
                  width: '85vw',
                  height: '60vh',
                  maxWidth: '500px',
                  maxHeight: '400px',
                  boxShadow: `0 0 20px ${getGlowColor()}`
                }}
              >
                {/* Corner brackets */}
                <div className={`absolute top-0 left-0 w-10 h-10 border-t-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute top-0 right-0 w-10 h-10 border-t-4 border-r-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute bottom-0 left-0 w-10 h-10 border-b-4 border-l-4 ${getBorderColor()} transition-colors`}></div>
                <div className={`absolute bottom-0 right-0 w-10 h-10 border-b-4 border-r-4 ${getBorderColor()} transition-colors`}></div>

                {/* Scan line animation */}
                <div
                  className="absolute left-2 right-2 h-0.5 bg-white/80"
                  style={{
                    boxShadow: '0 0 10px rgba(255, 255, 255, 0.6)',
                    animation: 'scanLine 1.5s ease-in-out infinite'
                  }}
                />
              </div>
            </div>
          </div>
        )}

        {/* Loading State */}
        {isLoading && (
          <div className="absolute inset-0 flex items-center justify-center text-white z-10 bg-black">
            <div className="text-center">
              <svg className="w-12 h-12 mx-auto mb-3 animate-pulse" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 13a3 3 0 11-6 0 3 3 0 016 0z" />
              </svg>
              <p>Memulai scanner...</p>
            </div>
          </div>
        )}

        {/* Permission Denied State */}
        {!isLoading && hasPermission === false && (
          <div className="absolute inset-0 flex items-center justify-center text-white p-6 z-10 bg-black">
            <div className="text-center max-w-md">
              <svg className="w-12 h-12 mx-auto mb-3 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
              <p className="mb-4">{error}</p>
              <button
                onClick={initializeScanner}
                className="px-6 py-3 bg-amber-500 text-white rounded-lg hover:bg-amber-600 active:bg-amber-700 transition-colors flex items-center gap-2 mx-auto"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                Coba Lagi
              </button>
              <button
                onClick={handleBack}
                className="mt-3 px-6 py-2 text-sm text-gray-400 hover:text-white transition-colors block mx-auto"
              >
                Kembali
              </button>
            </div>
          </div>
        )}

        {/* Product Lookup Modal */}
        {scannedCode && (
          <div className="absolute inset-0 flex items-center justify-center z-30 bg-black/80">
            <div className="bg-white rounded-xl p-6 mx-4 max-w-sm w-full shadow-2xl">
              {isLookingUp ? (
                <div className="text-center">
                  <svg className="animate-spin w-8 h-8 mx-auto mb-3 text-gray-600" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  <p className="text-sm text-gray-600">Mencari produk...</p>
                  <p className="text-xs text-gray-400 mt-1 font-mono">{scannedCode}</p>
                </div>
              ) : showSuccess ? (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <p className="text-sm text-gray-700 font-medium">Produk ditemukan!</p>
                  <p className="text-xs text-gray-400 font-mono">{scannedCode}</p>
                </div>
              ) : lookupError ? (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-yellow-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                  </svg>
                  <p className="text-sm text-gray-700 mb-1">{lookupError}</p>
                  <p className="text-xs text-gray-400 mb-4 font-mono">{scannedCode}</p>
                  <div className="flex gap-2">
                    <button
                      onClick={handleRescan}
                      className="flex-1 px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50"
                    >
                      Scan Ulang
                    </button>
                    <button
                      onClick={handleManualInput}
                      className="flex-1 px-4 py-2 bg-gray-800 text-white rounded-lg text-sm font-medium hover:bg-gray-700"
                    >
                      Input Manual
                    </button>
                  </div>
                </div>
              ) : (
                <div className="text-center">
                  <svg className="w-10 h-10 mx-auto mb-3 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <p className="text-sm text-gray-700">Barcode terdeteksi!</p>
                  <p className="text-xs text-gray-400 font-mono">{scannedCode}</p>
                </div>
              )}
            </div>
          </div>
        )}

        {/* CSS for scan line animation */}
        <style>{`
          @keyframes scanLine {
            0% { top: 10%; }
            50% { top: 90%; }
            100% { top: 10%; }
          }
        `}</style>

        {/* Scanning Status - Clean minimal indicator */}
        {isScanning && !scannedCode && (
          <div className="absolute bottom-32 left-0 right-0 text-center text-white z-20">
            <div className="inline-block px-6 py-3 rounded-full backdrop-blur-sm bg-black/60">
              <p className="text-sm font-medium">
                <ScanningDots />
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default SalesScanPage;
