/**
 * BarcodeScanner - Production-grade barcode scanner
 *
 * 3-Layer Detection Architecture:
 * 1. BarcodeDetector API (native, fastest - Chrome/Edge)
 * 2. ZXing Worker (WASM-accelerated preprocessing + JS decode)
 * 3. Backend pyzbar fallback (Phase 3)
 *
 * Features:
 * - ImageBitmap zero-copy transfer to worker
 * - ROI tracking with centered crop
 * - Consensus algorithm (N-of-M frames)
 * - Adaptive FPS
 * - Worker queue management
 */

import { BrowserMultiFormatReader } from '@zxing/browser';
import { BarcodeFormat, DecodeHintType } from '@zxing/library';

// Types
export interface ScannerOptions {
  formats?: BarcodeFormat[];
  desiredWidth?: number;
  desiredHeight?: number;
  decodeFPS?: number;
  roiResizeLongSide?: number;
  consensusFrames?: number;
  maxWorkerQueue?: number;
  tryHarderAfter?: number;
  // Backend fallback (Layer 3)
  backendFallbackEnabled?: boolean;
  backendFallbackURL?: string;
  backendFallbackAfterFailures?: number;
  backendFallbackTimeout?: number;
}

export interface ScanResult {
  text: string;
  format?: string;
  rawValue?: string;
  confidence?: number;
}

interface ResultEntry {
  payload: string;
  timestamp: number;
  result: ScanResult;
}

interface ROI {
  x: number;
  y: number;
  w: number;
  h: number;
}

// Default options
const DEFAULT_OPTIONS: Required<ScannerOptions> = {
  formats: [BarcodeFormat.EAN_13, BarcodeFormat.EAN_8, BarcodeFormat.CODE_128, BarcodeFormat.QR_CODE],
  desiredWidth: 1280,
  desiredHeight: 720,
  decodeFPS: 15,  // Increased for faster scanning
  roiResizeLongSide: 600,  // Reduced for faster processing
  consensusFrames: 2,
  maxWorkerQueue: 2,
  tryHarderAfter: 3,
  // Backend fallback defaults
  backendFallbackEnabled: true,
  backendFallbackURL: '/api/barcode/decode',
  backendFallbackAfterFailures: 2,  // Reduced from 3 - trigger backend earlier for difficult barcodes
  backendFallbackTimeout: 5000,
};

// BarcodeDetector type (native API)
declare global {
  interface Window {
    BarcodeDetector?: new (options?: { formats: string[] }) => {
      detect(source: ImageBitmap | HTMLVideoElement | HTMLCanvasElement): Promise<Array<{
        rawValue: string;
        format: string;
        boundingBox: DOMRectReadOnly;
      }>>;
    };
  }
}

export class BarcodeScanner {
  private video: HTMLVideoElement;
  private opts: Required<ScannerOptions>;
  private worker: Worker | null = null;
  private pendingWorkerJobs = 0;
  private lastResults: ResultEntry[] = [];
  private roi: ROI | null = null;
  private trackingStableCount = 0;

  private _onFound: (result: ScanResult) => void = () => {};
  private _onProgress: (state: { phase: string; data?: any }) => void = () => {};
  private _onError: (error: Error) => void = () => {};

  private _running = false;
  private _lastDecodeTime = 0;
  private stream: MediaStream | null = null;

  // Native BarcodeDetector (Layer 1)
  private barcodeDetector: InstanceType<NonNullable<Window['BarcodeDetector']>> | null = null;

  // ZXing reader for fallback (Layer 2)
  private zxingReader: BrowserMultiFormatReader | null = null;

  // Backend fallback state (Layer 3)
  private consecutiveFailures = 0;
  private backendInFlight = false;
  private backendCircuitOpen = false;
  private lastBackendBitmap: ImageBitmap | null = null;

  constructor(videoEl: HTMLVideoElement, opts: ScannerOptions = {}) {
    this.video = videoEl;
    this.opts = { ...DEFAULT_OPTIONS, ...opts };

    // Initialize native BarcodeDetector if available (Layer 1)
    if (typeof window !== 'undefined' && window.BarcodeDetector) {
      try {
        const nativeFormats = this.opts.formats.map(f => this.formatToNative(f)).filter(Boolean) as string[];
        this.barcodeDetector = new window.BarcodeDetector({ formats: nativeFormats });
        console.log('[Scanner] BarcodeDetector API available');
      } catch (e) {
        console.warn('[Scanner] BarcodeDetector init failed:', e);
      }
    }

    // Initialize ZXing reader (Layer 2)
    this.initZXing();
  }

  private formatToNative(format: BarcodeFormat): string | null {
    const map: Record<number, string> = {
      [BarcodeFormat.EAN_13]: 'ean_13',
      [BarcodeFormat.EAN_8]: 'ean_8',
      [BarcodeFormat.UPC_A]: 'upc_a',
      [BarcodeFormat.UPC_E]: 'upc_e',
      [BarcodeFormat.CODE_128]: 'code_128',
      [BarcodeFormat.CODE_39]: 'code_39',
      [BarcodeFormat.QR_CODE]: 'qr_code',
      [BarcodeFormat.DATA_MATRIX]: 'data_matrix',
    };
    return map[format] || null;
  }

  private initZXing(): void {
    try {
      const hints = new Map();
      hints.set(DecodeHintType.POSSIBLE_FORMATS, this.opts.formats);
      hints.set(DecodeHintType.TRY_HARDER, false); // Start with faster mode

      this.zxingReader = new BrowserMultiFormatReader(hints);
      console.log('[Scanner] ZXing reader initialized');
    } catch (e) {
      console.error('[Scanner] ZXing init failed:', e);
    }
  }

  /**
   * Start scanning
   */
  async start(): Promise<void> {
    try {
      await this.ensureCamera();
      this.startWorker();
      this._running = true;
      this._onProgress({ phase: 'started' });
      this.loop();
    } catch (err) {
      this._onError(err instanceof Error ? err : new Error(String(err)));
      throw err;
    }
  }

  /**
   * Stop scanning and release resources
   */
  stop(): void {
    this._running = false;

    if (this.worker) {
      this.worker.terminate();
      this.worker = null;
    }

    if (this.stream) {
      this.stream.getTracks().forEach(t => t.stop());
      this.stream = null;
    }

    this.video.srcObject = null;
    this._onProgress({ phase: 'stopped' });
  }

  /**
   * Register callback for successful scan
   */
  onFound(fn: (result: ScanResult) => void): void {
    this._onFound = fn;
  }

  /**
   * Register callback for progress updates
   */
  onProgress(fn: (state: { phase: string; data?: any }) => void): void {
    this._onProgress = fn;
  }

  /**
   * Register callback for errors
   */
  onError(fn: (error: Error) => void): void {
    this._onError = fn;
  }

  /**
   * Ensure camera is accessible
   */
  private async ensureCamera(): Promise<void> {
    if (this.stream) return;

    const constraints: MediaStreamConstraints = {
      video: {
        facingMode: 'environment',
        width: { ideal: this.opts.desiredWidth },
        height: { ideal: this.opts.desiredHeight },
      },
    };

    this.stream = await navigator.mediaDevices.getUserMedia(constraints);
    this.video.srcObject = this.stream;
    await this.video.play();

    this._onProgress({ phase: 'camera-ready' });
  }

  /**
   * Start preprocessing worker
   */
  private startWorker(): void {
    if (this.worker) return;

    try {
      this.worker = new Worker('/workers/zxing-worker.js');
      this.worker.onmessage = this.handleWorkerMessage.bind(this);
      this.worker.onerror = (e) => {
        console.error('[Scanner] Worker error:', e);
      };

      // Initialize worker
      this.worker.postMessage({
        type: 'init',
        formats: this.opts.formats.map(f => this.formatToNative(f)),
      });
    } catch (e) {
      console.warn('[Scanner] Worker creation failed, using main thread only:', e);
    }
  }

  /**
   * Handle messages from worker
   */
  private handleWorkerMessage(ev: MessageEvent): void {
    const msg = ev.data;

    if (msg.type === 'ready') {
      this._onProgress({ phase: 'worker-ready' });
    } else if (msg.type === 'processed') {
      this.pendingWorkerJobs = Math.max(0, this.pendingWorkerJobs - 1);

      // Decode using ZXing in main thread with preprocessed luminance data
      this.decodeFromLuminance(msg.luminanceSource, msg.width, msg.height);
    } else if (msg.type === 'result') {
      this.pendingWorkerJobs = Math.max(0, this.pendingWorkerJobs - 1);
      if (msg.result) {
        this.accumulateResult(msg.result);
      }
    }
  }

  /**
   * Decode from preprocessed luminance data using ZXing
   */
  private async decodeFromLuminance(
    luminanceSource: { luminances: Uint8ClampedArray; width: number; height: number },
    width: number,
    height: number
  ): Promise<void> {
    if (!this.zxingReader) return;

    try {
      // Create canvas from luminance for ZXing
      const canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      const ctx = canvas.getContext('2d');
      if (!ctx) return;

      // Create ImageData from luminance
      const imageData = ctx.createImageData(width, height);
      const data = imageData.data;
      const lum = luminanceSource.luminances;

      for (let i = 0; i < lum.length; i++) {
        const idx = i * 4;
        data[idx] = data[idx + 1] = data[idx + 2] = lum[i];
        data[idx + 3] = 255;
      }

      ctx.putImageData(imageData, 0, 0);

      // Decode
      const result = await this.zxingReader.decodeFromCanvas(canvas);
      if (result) {
        this.accumulateResult({
          text: result.getText(),
          format: BarcodeFormat[result.getBarcodeFormat()],
          rawValue: result.getText(),
        });
      }
    } catch (e) {
      // No barcode found - this is normal
    }
  }

  /**
   * Accumulate results for consensus
   */
  private accumulateResult(result: ScanResult): void {
    const payload = result.rawValue || result.text;
    if (!payload) return;

    const now = Date.now();

    // Add to results
    this.lastResults.push({ payload, timestamp: now, result });

    // Prune old results (1.5s window)
    const windowMs = 1500;
    this.lastResults = this.lastResults.filter(r => now - r.timestamp < windowMs);

    // Count occurrences
    const counts: Record<string, number> = {};
    for (const r of this.lastResults) {
      counts[r.payload] = (counts[r.payload] || 0) + 1;
    }

    // Check consensus
    for (const p in counts) {
      if (counts[p] >= this.opts.consensusFrames) {
        // Accepted!
        console.log('[Scanner] Consensus reached:', p, `(${counts[p]}/${this.opts.consensusFrames})`);
        this._onFound({ text: p, format: result.format, rawValue: p });

        // Clear window to avoid duplicates
        this.lastResults = [];
        break;
      }
    }
  }

  /**
   * Main scanning loop
   */
  private async loop(): Promise<void> {
    const frameInterval = 1000 / this.opts.decodeFPS;

    while (this._running) {
      const now = performance.now();

      if (now - this._lastDecodeTime < frameInterval) {
        await this.sleep(5);
        continue;
      }

      this._lastDecodeTime = now;

      // Capture frame
      let bitmap: ImageBitmap | null = null;
      try {
        bitmap = await createImageBitmap(this.video);
      } catch (err) {
        console.warn('[Scanner] Capture failed:', err);
        await this.sleep(30);
        continue;
      }

      let detectedSomething = false;

      // Layer 1: Try native BarcodeDetector first (fastest)
      if (this.barcodeDetector) {
        try {
          const detected = await this.barcodeDetector.detect(bitmap);
          if (detected && detected.length > 0) {
            detectedSomething = true;
            this.consecutiveFailures = 0;
            for (const d of detected) {
              this.accumulateResult({
                text: d.rawValue,
                format: d.format,
                rawValue: d.rawValue,
              });
            }
            bitmap.close();
            continue;
          }
        } catch (e) {
          console.warn('[Scanner] BarcodeDetector error:', e);
        }
      }

      // Layer 2: Send to worker for preprocessing, then ZXing decode
      if (this.worker && this.pendingWorkerJobs < this.opts.maxWorkerQueue) {
        // Compute ROI crop
        const cropRect = this.computeCrop(bitmap.width, bitmap.height);

        try {
          // Resize ROI before sending to worker
          const resized = await this.createResizedBitmap(bitmap, cropRect, this.opts.roiResizeLongSide);

          this.pendingWorkerJobs++;
          this.worker.postMessage({ type: 'decode', image: resized }, [resized]);
        } catch (e) {
          console.warn('[Scanner] Resize failed:', e);
        }
      } else {
        // Worker busy or unavailable, try direct decode
        try {
          if (this.zxingReader) {
            const canvas = document.createElement('canvas');
            canvas.width = bitmap.width;
            canvas.height = bitmap.height;
            const ctx = canvas.getContext('2d');
            if (ctx) {
              ctx.drawImage(bitmap, 0, 0);
              const result = await this.zxingReader.decodeFromCanvas(canvas);
              if (result) {
                detectedSomething = true;
                this.consecutiveFailures = 0;
                this.accumulateResult({
                  text: result.getText(),
                  format: BarcodeFormat[result.getBarcodeFormat()],
                  rawValue: result.getText(),
                });
              }
            }
          }
        } catch (e) {
          // No barcode found
        }
      }

      // Track failures and trigger backend fallback (Layer 3)
      if (!detectedSomething) {
        this.consecutiveFailures++;

        // Try backend fallback after N consecutive failures
        if (
          this.opts.backendFallbackEnabled &&
          this.consecutiveFailures >= this.opts.backendFallbackAfterFailures &&
          !this.backendInFlight &&
          !this.backendCircuitOpen
        ) {
          // Don't await - let it run in background
          this.tryBackendFallback(bitmap).catch(() => {});
        }
      }

      bitmap.close();
      await this.sleep(1);
    }
  }

  /**
   * Compute crop region (ROI)
   */
  private computeCrop(w: number, h: number): ROI {
    // If ROI is tracked and stable, use it
    if (this.roi && this.trackingStableCount > 0) {
      return this.roi;
    }

    // Default: centered crop at 75% of smaller dimension
    const size = Math.floor(Math.min(w, h) * 0.75);
    const x = Math.floor((w - size) / 2);
    const y = Math.floor((h - size) / 2);

    return { x, y, w: size, h: size };
  }

  /**
   * Create resized bitmap from ROI
   */
  private async createResizedBitmap(
    sourceBitmap: ImageBitmap,
    rect: ROI,
    longSideTarget: number
  ): Promise<ImageBitmap> {
    const { x, y, w, h } = rect;

    // Crop to ROI
    const crop = await createImageBitmap(sourceBitmap, x, y, w, h);

    // Compute resize scale
    const longSide = Math.max(w, h);
    const scale = Math.min(1, longSideTarget / longSide);
    const tw = Math.max(1, Math.round(w * scale));
    const th = Math.max(1, Math.round(h * scale));

    // Resize using OffscreenCanvas or regular canvas
    let targetBitmap: ImageBitmap;

    if (typeof OffscreenCanvas !== 'undefined') {
      const offscreen = new OffscreenCanvas(tw, th);
      const ctx = offscreen.getContext('2d') as OffscreenCanvasRenderingContext2D | null;
      if (ctx) {
        ctx.drawImage(crop, 0, 0, tw, th);
        targetBitmap = await createImageBitmap(offscreen);
      } else {
        targetBitmap = crop;
      }
    } else {
      const canvas = document.createElement('canvas');
      canvas.width = tw;
      canvas.height = th;
      const ctx = canvas.getContext('2d');
      if (ctx) {
        ctx.drawImage(crop, 0, 0, tw, th);
        targetBitmap = await createImageBitmap(canvas);
      } else {
        targetBitmap = crop;
      }
    }

    crop.close();
    return targetBitmap;
  }

  /**
   * Sleep utility
   */
  private sleep(ms: number): Promise<void> {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  /**
   * Try backend pyzbar fallback (Layer 3)
   */
  private async tryBackendFallback(bitmap: ImageBitmap): Promise<void> {
    if (this.backendInFlight || this.backendCircuitOpen) return;
    if (!this.opts.backendFallbackEnabled) return;

    this.backendInFlight = true;
    this._onProgress({ phase: 'backend-fallback', data: { failures: this.consecutiveFailures } });

    try {
      // Compute ROI crop
      const cropRect = this.computeCrop(bitmap.width, bitmap.height);

      // Crop to ROI
      const cropped = await createImageBitmap(
        bitmap,
        cropRect.x,
        cropRect.y,
        cropRect.w,
        cropRect.h
      );

      // Convert to WEBP blob (compressed)
      const canvas = document.createElement('canvas');
      canvas.width = cropped.width;
      canvas.height = cropped.height;
      const ctx = canvas.getContext('2d');
      if (!ctx) {
        cropped.close();
        return;
      }
      ctx.drawImage(cropped, 0, 0);
      cropped.close();

      // Convert to blob
      const blob = await new Promise<Blob | null>((resolve) => {
        canvas.toBlob(resolve, 'image/webp', 0.75);
      });

      if (!blob) {
        console.warn('[Scanner] Failed to create blob');
        return;
      }

      // Upload to backend
      const formData = new FormData();
      formData.append('file', blob, 'roi.webp');

      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), this.opts.backendFallbackTimeout);

      const response = await fetch(this.opts.backendFallbackURL, {
        method: 'POST',
        body: formData,
        signal: controller.signal,
      });

      clearTimeout(timeoutId);

      if (!response.ok) {
        throw new Error(`Backend error: ${response.status}`);
      }

      const data = await response.json();

      // data: {ok: bool, results: [{text, type, rect}], latency_ms: number, path: string}
      if (data.ok && data.results && data.results.length > 0) {
        const result = data.results[0];
        console.log('[Scanner] Backend found:', result.text, `(${data.latency_ms}ms, path: ${data.path})`);

        this._onProgress({
          phase: 'backend-success',
          data: { latency: data.latency_ms, cached: data.cached, path: data.path }
        });

        // Accumulate result
        this.accumulateResult({
          text: result.text,
          format: result.type,
          rawValue: result.text,
        });

        // Reset failure counter on success
        this.consecutiveFailures = 0;
      } else {
        console.log('[Scanner] Backend returned no results');
        this._onProgress({ phase: 'backend-no-result' });
      }

    } catch (err: any) {
      console.error('[Scanner] Backend fallback error:', err);
      this._onProgress({ phase: 'backend-error', data: { error: String(err) } });

      // Circuit breaker: disable backend for 30s after error
      this.backendCircuitOpen = true;
      setTimeout(() => {
        this.backendCircuitOpen = false;
        console.log('[Scanner] Backend circuit breaker reset');
      }, 30000);

    } finally {
      this.backendInFlight = false;
    }
  }
}

export default BarcodeScanner;
