import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { soundFeedback } from '../../../utils/soundFeedback';
import ScannerSection from './ScannerSection';
import CartSection from './CartSection';
import PaymentSection from './PaymentSection';
import RecentProducts from './RecentProducts';
import { CartItem, Product } from './types';

interface SalesTransactionProps {
  tenantId: string;
  onClose: () => void;
  isEmbedded?: boolean; // Desktop split view mode (50% width)
}

const SalesTransaction: React.FC<SalesTransactionProps> = ({ tenantId, onClose, isEmbedded = false }) => {
  const navigate = useNavigate();

  // State
  const [cart, setCart] = useState<CartItem[]>([]);
  const [isScanning, setIsScanning] = useState(false);
  const [recentProducts, setRecentProducts] = useState<Product[]>([]);
  const [paymentAmount, setPaymentAmount] = useState(0);
  const [showPayment, setShowPayment] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false); // FIX #1: Loading state
  const [showSuccess, setShowSuccess] = useState(false);
  const [errorMessage, setErrorMessage] = useState<string>('');
  const [receiptHtml, setReceiptHtml] = useState<string>('');

  // Manual Input Modal State (Bug #3, #4, #6)
  const [showManualInput, setShowManualInput] = useState(false);
  const [manualBarcode, setManualBarcode] = useState('');
  const [productSearchQuery, setProductSearchQuery] = useState('');
  const [productSearchResults, setProductSearchResults] = useState<Product[]>([]);
  const [isSearching, setIsSearching] = useState(false);

  // Debounce ref for barcode scanning
  const lastScanRef = useRef<{ barcode: string; time: number }>({ barcode: '', time: 0 });
  const DEBOUNCE_MS = 2000; // 2 seconds debounce for same barcode
  const barcodeInputRef = useRef<HTMLInputElement>(null);
  const productSearchRef = useRef<HTMLInputElement>(null);

  // Computed
  const totalAmount = cart.reduce((sum, item) => sum + (item.price * item.qty), 0);
  const kembalian = paymentAmount - totalAmount;

  // Load recent products on mount
  useEffect(() => {
    loadRecentProducts();
  }, [tenantId]);

  const loadRecentProducts = async () => {
    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/recent-sales?limit=5&tenant_id=${tenantId}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
        }
      });
      if (response.ok) {
        const data = await response.json();
        setRecentProducts(data.products || []);
      }
    } catch (e) {
      console.error('Failed to load recent products:', e);
    }
  };

  /**
   * Handle barcode scan result
   * - Debounce same barcode within 2 seconds
   * - Auto-add qty=1
   * - If item exists, increment qty
   * - Play sound signature
   *
   * IMPORTANT: Uses functional setCart updates to avoid cart dependency
   * This prevents unnecessary re-renders and debounce issues
   */
  const handleBarcodeScanned = useCallback(async (barcode: string) => {
    const now = Date.now();
    const last = lastScanRef.current;

    // DEBOUNCE: Skip if same barcode scanned within 2 seconds
    if (barcode === last.barcode && (now - last.time) < DEBOUNCE_MS) {
      console.log('[POS] Debounced - same barcode within 2s:', barcode);
      return; // SKIP duplicate scan
    }

    // Update last scan reference IMMEDIATELY before any async work
    lastScanRef.current = { barcode, time: now };

    setErrorMessage('');

    // Use functional update to check cart without depending on it
    let itemExists = false;
    setCart(prev => {
      const existing = prev.find(item => item.barcode === barcode);
      if (existing) {
        itemExists = true;
        // Increment qty
        return prev.map(item =>
          item.barcode === barcode
            ? { ...item, qty: item.qty + 1 }
            : item
        );
      }
      return prev; // No change yet, will add after API call
    });

    if (itemExists) {
      soundFeedback.scanSuccess();
      return;
    }

    // Lookup product (item not in cart)
    try {
      const token = localStorage.getItem('access_token');
      const response = await fetch(`/api/products/barcode/${barcode}?tenant_id=${tenantId}`, {
        headers: {
          'Authorization': `Bearer ${token}`,
        }
      });

      if (!response.ok) {
        soundFeedback.scanError();
        setErrorMessage('Produk tidak ditemukan');
        setTimeout(() => setErrorMessage(''), 2000);
        return;
      }

      const product = await response.json();

      // Add to cart with qty=1
      const newItem: CartItem = {
        id: `cart_${Date.now()}`,
        productId: product.id,
        name: product.nama_produk || product.name,
        barcode: barcode,
        price: product.harga_jual || product.price || 0,
        qty: 1
      };

      setCart(prev => [...prev, newItem]);
      soundFeedback.scanSuccess();

    } catch (e) {
      console.error('Product lookup failed:', e);
      soundFeedback.scanError();
      setErrorMessage('Gagal mencari produk');
      setTimeout(() => setErrorMessage(''), 2000);
    }
  }, [tenantId]); // Removed cart and DEBOUNCE_MS from deps - uses refs instead

  /**
   * Add product from recent list (quick add)
   */
  const handleQuickAdd = (product: Product) => {
    const existingItem = cart.find(item => item.productId === product.id);

    if (existingItem) {
      setCart(prev => prev.map(item =>
        item.productId === product.id
          ? { ...item, qty: item.qty + 1 }
          : item
      ));
    } else {
      const newItem: CartItem = {
        id: `cart_${Date.now()}`,
        productId: product.id,
        name: product.name,
        barcode: product.barcode,
        price: product.price,
        qty: 1
      };
      setCart(prev => [...prev, newItem]);
    }

    soundFeedback.itemAdded();
  };

  /**
   * Bug #6: Handle manual barcode input with auto-search at 13 digits
   */
  const handleManualBarcodeChange = (value: string) => {
    // Only allow digits
    const digits = value.replace(/\D/g, '');
    setManualBarcode(digits);

    // Auto-search when barcode reaches 13 digits (Bug #6)
    if (digits.length === 13) {
      handleBarcodeScanned(digits);
      setManualBarcode('');
      setShowManualInput(false);
    }
  };

  /**
   * Search products by name for POS autocomplete
   * Uses /api/products/search/pos which returns products from inventory
   * (only products with harga_jual set can be sold)
   */
  const handleProductSearch = useCallback(async (query: string) => {
    setProductSearchQuery(query);

    if (query.length < 2) {
      setProductSearchResults([]);
      return;
    }

    setIsSearching(true);
    try {
      const token = localStorage.getItem('access_token');
      // Use POS-specific endpoint that returns id, name, barcode, harga_jual
      const response = await fetch(`/api/products/search/pos?q=${encodeURIComponent(query)}&limit=5`, {
        headers: {
          'Authorization': `Bearer ${token}`,
        }
      });

      if (response.ok) {
        const data = await response.json();
        // Map to Product format (API returns {products: [{id, name, barcode, harga_jual, stok}]})
        const products = (data.products || []).map((p: any) => ({
          id: p.id,
          name: p.name,
          barcode: p.barcode || '',
          price: p.harga_jual || 0
        }));
        setProductSearchResults(products);
      }
    } catch (e) {
      console.error('Product search failed:', e);
    } finally {
      setIsSearching(false);
    }
  }, []);

  /**
   * Add product from search result
   */
  const handleAddFromSearch = (product: Product) => {
    handleQuickAdd(product);
    setProductSearchQuery('');
    setProductSearchResults([]);
    setShowManualInput(false);
  };

  /**
   * Update item quantity (inline edit)
   */
  const handleQtyChange = (itemId: string, newQty: number) => {
    if (newQty <= 0) {
      // Remove item
      setCart(prev => prev.filter(item => item.id !== itemId));
    } else {
      setCart(prev => prev.map(item =>
        item.id === itemId ? { ...item, qty: newQty } : item
      ));
    }
  };

  /**
   * Remove item from cart
   */
  const handleRemoveItem = (itemId: string) => {
    setCart(prev => prev.filter(item => item.id !== itemId));
  };

  /**
   * Payment shortcut tap (adder mode)
   */
  const handlePaymentShortcut = (amount: number) => {
    setPaymentAmount(prev => prev + amount);
    soundFeedback.tap();
  };

  /**
   * Set exact amount (Uang Pas)
   */
  const handleUangPas = () => {
    setPaymentAmount(totalAmount);
    soundFeedback.tap();
  };

  /**
   * Reset payment amount
   */
  const handlePaymentReset = () => {
    setPaymentAmount(0);
  };

  /**
   * Complete transaction - FIX #1: Loading state
   */
  const handleCompleteTransaction = async (paymentMethod: 'TUNAI' | 'TRANSFER' | 'QRIS' = 'TUNAI', proofImage?: string) => {
    // Prevent double submit
    if (isSubmitting) return;

    // For TUNAI, check payment amount; for others, proofImage is already validated in PaymentSection
    if (paymentMethod === 'TUNAI' && paymentAmount < totalAmount) {
      soundFeedback.scanError();
      return;
    }

    // FIX #1: Set loading state
    setIsSubmitting(true);

    try {
      const token = localStorage.getItem('access_token');
      const payload = {
        items: cart.map(item => ({
          productId: item.productId,
          name: item.name,
          barcode: item.barcode,
          qty: item.qty,
          price: item.price
        })),
        totalAmount,
        paymentAmount: paymentMethod === 'TUNAI' ? paymentAmount : totalAmount,
        kembalian: paymentMethod === 'TUNAI' ? kembalian : 0,
        paymentMethod: paymentMethod === 'TUNAI' ? 'CASH' : paymentMethod,
        proofImage: proofImage
      };

      console.log('[POS] Submitting transaction...');

      const response = await fetch(`/api/transactions/sales`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload)
      });

      const result = await response.json();

      console.log('[POS] Transaction response:', {
        ok: response.ok,
        status: response.status,
        resultStatus: result.status
      });

      if (response.ok && result.status === 'success') {
        soundFeedback.transactionComplete();

        // Store receipt HTML for display
        const receiptContent = result.message || result.receipt_html || '';
        if (receiptContent) {
          setReceiptHtml(receiptContent);
        }

        // Show success with receipt
        setShowPayment(false);
        setShowSuccess(true);

      } else {
        console.error('[POS] Transaction failed:', result);
        soundFeedback.scanError();
        setErrorMessage(result.message || result.detail || 'Transaksi gagal');
        setTimeout(() => setErrorMessage(''), 3000);
      }
    } catch (e) {
      console.error('[POS] Transaction error:', e);
      soundFeedback.scanError();
      setErrorMessage('Transaksi gagal');
      setTimeout(() => setErrorMessage(''), 3000);
    } finally {
      // FIX #1: Always reset loading state
      setIsSubmitting(false);
    }
  };

  return (
    <div className={`sales-transaction bg-gray-100 flex flex-col relative ${isEmbedded ? 'h-full w-full' : 'fixed inset-0 z-50'}`}>
      {/* Header - Fixed 60px height to align with ChatPanel header */}
      <div
        className="bg-white border-b border-gray-200 px-4 flex items-center gap-3 flex-shrink-0"
        style={{ height: '60px' }}
      >
        <button
          onClick={onClose}
          className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          title={isEmbedded ? 'Tutup POS' : 'Kembali'}
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            {isEmbedded ? (
              // X icon for embedded mode (close split view)
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            ) : (
              // Back arrow for modal mode
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            )}
          </svg>
        </button>
        <div className="flex-1">
          <h1 className="font-semibold text-lg"> Jual Produk</h1>
        </div>
        {/* Manual Input Button */}
        <button
          onClick={() => {
            setShowManualInput(true);
            // Focus barcode input after modal opens
            setTimeout(() => barcodeInputRef.current?.focus(), 100);
          }}
          className="px-3 py-2 bg-gray-100 hover:bg-gray-200 text-gray-700 text-sm font-medium rounded-lg transition-colors"
        >
          Input Manual
        </button>
      </div>

      {/* Error Toast */}
      {errorMessage && (
        <div className="absolute top-20 left-4 right-4 z-50 bg-red-500 text-white px-4 py-2 rounded-lg text-center text-sm shadow-lg">
          {errorMessage}
        </div>
      )}

      {/* Success Overlay with Premium Receipt */}
      {showSuccess && (
        <div className="absolute inset-0 z-50 bg-gray-50 flex flex-col overflow-auto">
          {/* Success Header */}
          <div className="bg-green-500 text-white py-6 text-center flex-shrink-0">
            <div className="text-5xl mb-2"></div>
            <div className="text-xl font-bold">Transaksi Berhasil!</div>
            <div className="text-green-100">Kembalian: Rp{kembalian.toLocaleString('id-ID')}</div>
          </div>

          {/* Premium Receipt Card */}
          <div className="flex-1 p-4 overflow-auto">
            <div className="bg-white rounded-xl shadow-sm border max-w-md mx-auto overflow-hidden">
              {/* Receipt from backend */}
              {receiptHtml && (
                <div
                  className="receipt-content p-4"
                  dangerouslySetInnerHTML={{ __html: receiptHtml }}
                  style={{
                    // CSS to style backend receipt - items left, values right
                  }}
                />
              )}

              {/* If no HTML, show cart summary */}
              {!receiptHtml && cart.length > 0 && (
                <div className="p-5">
                  <div className="text-center border-b pb-4 mb-4">
                    <p className="text-gray-500 text-sm">Penjualan</p>
                    <p className="text-gray-400 text-sm mt-1">{new Date().toLocaleDateString('id-ID')}</p>
                  </div>

                  {/* Items - Left aligned name, Right aligned values */}
                  <div className="space-y-3">
                    {cart.map((item, idx) => (
                      <div key={idx} className="flex justify-between items-start">
                        <div className="flex-1">
                          <div className="font-medium text-gray-900">{item.name}</div>
                          <div className="text-sm text-gray-500">{item.qty}  Rp{item.price.toLocaleString('id-ID')}</div>
                        </div>
                        <div className="text-right font-medium text-gray-900">
                          Rp{(item.qty * item.price).toLocaleString('id-ID')}
                        </div>
                      </div>
                    ))}
                  </div>

                  <div className="border-t my-4"></div>

                  {/* Total */}
                  <div className="flex justify-between items-center">
                    <span className="font-bold text-gray-900">TOTAL</span>
                    <span className="font-bold text-xl text-green-600">
                      Rp{totalAmount.toLocaleString('id-ID')}
                    </span>
                  </div>
                </div>
              )}

              {/* Footer */}
              <div className="border-t p-4 text-center bg-gray-50">
                <p className="text-gray-500 text-sm italic">Terima kasih! </p>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="p-4 space-y-2 bg-white border-t flex-shrink-0">
            <button
              onClick={() => {
                setCart([]);
                setPaymentAmount(0);
                setShowSuccess(false);
                setReceiptHtml('');
                loadRecentProducts();
              }}
              className="w-full py-3 bg-green-500 hover:bg-green-600 text-white rounded-xl font-bold transition-colors"
            >
               Transaksi Baru
            </button>

            <button
              onClick={() => {
                setCart([]);
                setPaymentAmount(0);
                setShowSuccess(false);
                setReceiptHtml('');
                onClose(); // Close POS and return to dashboard
              }}
              className="w-full py-3 bg-gray-100 hover:bg-gray-200 text-gray-700 rounded-xl font-medium transition-colors"
            >
               Kembali ke Dashboard
            </button>
          </div>
        </div>
      )}

      {/* Manual Input Modal - Bug #3, #4, #6 */}
      {showManualInput && (
        <div className="absolute inset-0 z-50 bg-black/50 flex items-start justify-center pt-20">
          <div className="bg-white rounded-xl shadow-xl w-[90%] max-w-md overflow-hidden">
            {/* Header */}
            <div className="flex items-center justify-between p-4 border-b">
              <h3 className="font-semibold text-lg">Input Manual</h3>
              <button
                onClick={() => {
                  setShowManualInput(false);
                  setManualBarcode('');
                  setProductSearchQuery('');
                  setProductSearchResults([]);
                }}
                className="p-1 hover:bg-gray-100 rounded-full"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="p-4 space-y-4">
              {/* FIX #4: Product Name Search FIRST (di atas) - untuk space autocomplete */}
              <div className="relative">
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Cari Nama Produk
                </label>
                <input
                  ref={productSearchRef}
                  type="text"
                  value={productSearchQuery}
                  onChange={(e) => handleProductSearch(e.target.value)}
                  placeholder="Ketik nama produk..."
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-green-500"
                  autoComplete="off"
                  autoFocus
                />

                {/* Search Results - Autocomplete dropdown (immediately below input) */}
                {isSearching && (
                  <div className="text-center py-2 text-gray-500 text-sm">
                    Mencari...
                  </div>
                )}
                {productSearchResults.length > 0 && (
                  <div className="absolute top-full left-0 right-0 mt-1 bg-white border border-gray-200 rounded-lg shadow-lg divide-y max-h-48 overflow-y-auto z-10">
                    {productSearchResults.map((product) => (
                      <button
                        key={product.id}
                        onClick={() => handleAddFromSearch(product)}
                        className="w-full px-4 py-3 text-left hover:bg-gray-50 flex items-center justify-between"
                      >
                        <div>
                          <div className="font-medium">{product.name}</div>
                          <div className="text-xs text-gray-500">{product.barcode || 'No barcode'}</div>
                        </div>
                        <div className="text-green-600 font-semibold">
                          Rp{product.price.toLocaleString('id-ID')}
                        </div>
                      </button>
                    ))}
                  </div>
                )}
                {productSearchQuery.length >= 2 && !isSearching && productSearchResults.length === 0 && (
                  <div className="text-center py-2 text-gray-500 text-sm">
                    Produk tidak ditemukan
                  </div>
                )}
              </div>

              {/* Divider */}
              <div className="flex items-center gap-3">
                <div className="flex-1 h-px bg-gray-200"></div>
                <span className="text-sm text-gray-400">atau</span>
                <div className="flex-1 h-px bg-gray-200"></div>
              </div>

              {/* FIX #4: Barcode Input SECOND (di bawah) */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Scan/Input Barcode
                </label>
                <input
                  ref={barcodeInputRef}
                  type="text"
                  inputMode="numeric"
                  pattern="[0-9]*"
                  value={manualBarcode}
                  onChange={(e) => handleManualBarcodeChange(e.target.value)}
                  placeholder="Ketik 13 digit barcode..."
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg text-lg text-center tracking-widest focus:ring-2 focus:ring-green-500 focus:border-green-500"
                  maxLength={13}
                  autoComplete="off"
                />
                <div className="text-xs text-gray-500 mt-1 text-center">
                  {manualBarcode.length}/13 digit {manualBarcode.length === 13 && ''}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Scanner Section - Batch Mode */}
        <ScannerSection
          isActive={isScanning}
          onToggle={() => setIsScanning(!isScanning)}
          onBarcodeDetected={handleBarcodeScanned}
        />

        {/* Recent Products - Quick Add */}
        {!isScanning && (
          <RecentProducts
            products={recentProducts}
            onSelect={handleQuickAdd}
          />
        )}

        {/* Cart - Always Visible */}
        <CartSection
          items={cart}
          total={totalAmount}
          onQtyChange={handleQtyChange}
          onRemove={handleRemoveItem}
          onCheckout={() => setShowPayment(true)}
        />
      </div>

      {/* Payment Section */}
      {showPayment && (
        <PaymentSection
          total={totalAmount}
          paymentAmount={paymentAmount}
          kembalian={kembalian}
          isSubmitting={isSubmitting}
          onShortcutTap={handlePaymentShortcut}
          onUangPas={handleUangPas}
          onManualInput={setPaymentAmount}
          onReset={handlePaymentReset}
          onComplete={handleCompleteTransaction}
          onCancel={() => setShowPayment(false)}
        />
      )}
    </div>
  );
};

export default SalesTransaction;
