import TextMessage from '../../features/chat/MessageTypes/TextMessage'
import FinancialReport from '../../features/chat/MessageTypes/FinancialReport'

interface Message {
  id?: string
  text: string
  isFromUser: boolean
  timestamp?: string
  type?: 'text' | 'financial_report'
}

interface MessageBubbleProps {
  message: Message
}

export default function MessageBubble({ message }: MessageBubbleProps) {
  const isUser = message.isFromUser
  const isFinancialReport = message.type === 'financial_report' ||
    (message.text.includes('') || message.text.includes(''))

  // Detect if message contains HTML (from backend receipts)
  const isHtmlContent = message.text.includes('<table') ||
    message.text.includes('<div') ||
    message.text.includes('<br')

  return (
    <div className={`flex flex-col mb-4 ${isUser ? 'items-end' : 'items-start'}`}>
      <div className={`rounded-2xl px-4 py-2 max-w-[80%] ${
        isUser
          ? 'bg-purple-600 text-white ml-auto'
          : 'bg-gray-100 text-gray-900 mr-auto'
      }`}>
        {isHtmlContent ? (
          <div
            dangerouslySetInnerHTML={{ __html: message.text }}
            className="text-sm leading-relaxed"
          />
        ) : isFinancialReport ? (
          <FinancialReport content={message.text} />
        ) : (
          <TextMessage content={message.text} />
        )}
      </div>
      {message.timestamp && (
        <span className="text-xs text-gray-500 mt-1">
          {message.timestamp}
        </span>
      )}
    </div>
  )
}
