/**
 * MilkyHoop Sound Signature
 * "du-ding!" - Unique 2-tone chime for brand recognition
 */

class SoundFeedback {
  private audioContext: AudioContext | null = null;

  private getAudioContext(): AudioContext {
    if (!this.audioContext) {
      this.audioContext = new AudioContext();
    }
    // Resume if suspended (mobile requirement)
    if (this.audioContext.state === 'suspended') {
      this.audioContext.resume();
    }
    return this.audioContext;
  }

  /**
   * Play a tone at specified frequency and duration
   */
  private playTone(frequency: number, duration: number, startDelay: number = 0): void {
    try {
      const ctx = this.getAudioContext();
      const oscillator = ctx.createOscillator();
      const gainNode = ctx.createGain();

      oscillator.connect(gainNode);
      gainNode.connect(ctx.destination);

      oscillator.frequency.value = frequency;
      oscillator.type = 'sine';

      const startTime = ctx.currentTime + startDelay / 1000;
      const endTime = startTime + duration / 1000;

      gainNode.gain.setValueAtTime(0.3, startTime);
      gainNode.gain.exponentialRampToValueAtTime(0.01, endTime);

      oscillator.start(startTime);
      oscillator.stop(endTime);
    } catch (e) {
      console.warn('Sound playback failed:', e);
    }
  }

  /**
   * MilkyHoop Signature Sound - "du-ding!"
   * Used for successful barcode scan
   */
  scanSuccess(): void {
    this.playTone(800, 50, 0);    // "du" - Low tone
    this.playTone(1200, 80, 60);  // "ding!" - High tone
  }

  /**
   * Error sound - "du-dut"
   * Used for scan failure or validation error
   */
  scanError(): void {
    this.playTone(400, 100, 0);   // "du" - Low
    this.playTone(300, 150, 110); // "dut" - Lower
  }

  /**
   * Transaction complete - "ding-ding-ding!"
   * Celebratory 3-tone chime
   */
  transactionComplete(): void {
    this.playTone(1000, 80, 0);    // "ding"
    this.playTone(1200, 80, 100);  // "ding"
    this.playTone(1400, 120, 200); // "ding!"
  }

  /**
   * Button tap feedback - subtle click
   */
  tap(): void {
    this.playTone(600, 20, 0);
  }

  /**
   * Item added to cart - quick blip
   */
  itemAdded(): void {
    this.playTone(1000, 40, 0);
  }
}

export const soundFeedback = new SoundFeedback();
