import React, { useState, useEffect, useRef } from 'react';

interface ActionMenuButtonProps {
  isMobile?: boolean;
  onMenuItemClick?: (itemId: string) => void;
}

const menuItems = [
  // Row 1
  { id: 'sales_product', label: 'POS', icon: '/icons/Jual%20produk.png', disabled: false },
  { id: 'purchase_goods', label: 'Kulakan', icon: '/icons/Beli%20barang.png', disabled: false },
  { id: 'expense', label: 'Beban', icon: '/icons/Beban.png', disabled: false },
  { id: 'inventory_update', label: 'Inventory', icon: '/icons/Update%20Stok.png', disabled: false },
  // Row 2
  { id: 'cash_bank', label: 'Kas & Bank', icon: '/icons/Modal.png', disabled: false },
  { id: 'debt_receivable', label: 'Hutang/\nPiutang', icon: '/icons/Neraca.png', disabled: false },
  { id: 'customer', label: 'Pelanggan', icon: '/icons/Prive.png', disabled: false },
  { id: 'insight', label: 'Insight', icon: '/icons/Laba%20rugi.png', disabled: false },
];

const ActionMenuButton: React.FC<ActionMenuButtonProps> = ({
  isMobile = false,
  onMenuItemClick
}) => {
  const [isOpen, setIsOpen] = useState(false);
  const menuRef = useRef<HTMLDivElement>(null);

  // Close on outside click
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  const handleItemClick = (itemId: string) => {
    console.log('[ActionMenuButton] Menu item clicked:', itemId);
    // TODO: Future implementation - render form card
    setIsOpen(false);
    onMenuItemClick?.(itemId);
  };

  return (
    <div ref={menuRef} className="relative">
      {/* [+] Button */}
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="w-10 h-10 flex items-center justify-center bg-transparent hover:bg-gray-200/50 text-gray-700 rounded-full transition-colors"
        aria-label="Open action menu"
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
        </svg>
      </button>

      {/* Menu Popup */}
      {isOpen && (
        <>
          {isMobile ? (
            // Mobile: Bottom sheet with GRID LAYOUT (WhatsApp style)
            <div className="fixed inset-0 z-[100] flex items-end">
              {/* Backdrop */}
              <div
                className="absolute inset-0 bg-black/20"
                onClick={() => setIsOpen(false)}
              />

              {/* Bottom Sheet */}
              <div
                className="relative w-full bg-white rounded-t-2xl shadow-2xl animate-slide-up"
                style={{
                  maxHeight: '70vh',
                  paddingBottom: 'env(safe-area-inset-bottom, 16px)'
                }}
              >
                {/* Drag Handle */}
                <div className="flex justify-center pt-3 pb-2">
                  <div className="w-10 h-1 bg-gray-300 rounded-full" />
                </div>

                {/* Menu Items - GRID LAYOUT (4 columns) */}
                <div className="px-4 pb-6 pt-2">
                  <div className="grid grid-cols-4 gap-3">
                    {menuItems.map((item) => (
                      <button
                        key={item.id}
                        onClick={() => handleItemClick(item.id)}
                        disabled={item.disabled}
                        className="flex flex-col items-center justify-start text-center disabled:opacity-40 disabled:cursor-not-allowed"
                        style={{ minHeight: '80px' }}
                      >
                        {/* Icon */}
                        <div className="w-12 h-12 mb-2 flex items-center justify-center">
                          <img
                            src={item.icon}
                            alt={item.label}
                            className="w-full h-full object-contain"
                          />
                        </div>

                        {/* Label - Multi-line, centered */}
                        <div className="text-xs font-normal text-gray-800 leading-tight px-1 whitespace-pre-line">
                          {item.label}
                        </div>
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          ) : (
            // Desktop: Popup menu with SLIDE-UP ANIMATION
            <div
              className="absolute bottom-full left-0 mb-2 bg-white rounded-2xl py-1 z-50 animate-popup-slide"
              style={{
                minWidth: '220px',
                maxWidth: '220px',
                boxShadow: '0 4px 12px rgba(0, 0, 0, 0.25), 0 2px 6px rgba(0, 0, 0, 0.15)',
              }}
            >
              {menuItems.map((item, index) => (
                <button
                  key={item.id}
                  onClick={() => handleItemClick(item.id)}
                  disabled={item.disabled}
                  className={`w-full flex items-center gap-3 px-5 py-3 hover:bg-gray-50 transition-colors text-left disabled:opacity-40 disabled:cursor-not-allowed ${
                    index === 0 ? 'rounded-t-2xl' : ''
                  } ${
                    index === menuItems.length - 1 ? 'rounded-b-2xl' : ''
                  }`}
                >
                  <div className="w-6 h-6 flex-shrink-0">
                    <img
                      src={item.icon}
                      alt={item.label}
                      className="w-full h-full object-contain"
                    />
                  </div>
                  <span className="text-[15px] font-normal text-gray-800">{item.label}</span>
                </button>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  );
};

export default ActionMenuButton;