import React, { useState, useEffect } from 'react';

const InstallPrompt: React.FC = () => {
  const [isIOS, setIsIOS] = useState(false);
  const [deferredPrompt, setDeferredPrompt] = useState<any>(null);
  const [showModal, setShowModal] = useState(false);
  const [isInstalled, setIsInstalled] = useState(
    localStorage.getItem('pwa-installed') === 'true'
  );

  useEffect(() => {
    const isIOSDevice = /iPad|iPhone|iPod/.test(navigator.userAgent) && !(window as any).MSStream;
    setIsIOS(isIOSDevice);

    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault();
      setDeferredPrompt(e);
    };

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    };
  }, []);

  const handleClick = () => {
    if (isInstalled) {
      // Redirect to PWA
      window.location.href = '/';
      return;
    }

    if (isIOS) {
      setShowModal(true);
      return;
    }

    if (deferredPrompt) {
      deferredPrompt.prompt();
      deferredPrompt.userChoice.then(({ outcome }: any) => {
        if (outcome === 'accepted') {
          localStorage.setItem('pwa-installed', 'true');
          setIsInstalled(true);
        }
      });
    }
  };

  const isStandalone = window.matchMedia('(display-mode: standalone)').matches;

  if (isStandalone) {
    return null;
  }

  return (
    <>
      <div className="md:hidden bg-gradient-to-r from-emerald-500 to-emerald-600 text-white">
        <button
          onClick={handleClick}
          className="w-full px-4 py-3 text-left flex items-center justify-between hover:bg-black/5 transition-colors"
        >
          <div className="flex-1">
            <div className="font-medium text-[15px]">
              {isInstalled
                ? 'Pakai di aplikasi lebih cepat'
                : (isIOS ? 'Share and Add to Home Screen' : 'Install MilkyHoop')
              }
            </div>
            <div className="text-xs opacity-90 mt-0.5">
              {isInstalled ? 'Kamu sudah install' : 'Akses lebih cepat!'}
            </div>
          </div>
          <svg className="w-5 h-5 flex-shrink-0 ml-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>

      {/* iOS Install Modal */}
      {showModal && (
        <div
          className="fixed inset-0 bg-black/50 z-[100] flex items-end md:items-center justify-center p-4"
          onClick={() => setShowModal(false)}
        >
          <div
            className="bg-white rounded-t-2xl md:rounded-2xl p-6 max-w-sm w-full animate-slide-up"
            onClick={e => e.stopPropagation()}
          >
            <h3 className="text-lg font-semibold mb-4">Cara Install MilkyHoop</h3>
            <ol className="space-y-3 text-sm text-gray-700">
              <li className="flex gap-3">
                <span className="font-semibold text-emerald-600">1.</span>
                <span>Tap tombol <strong>Share</strong> (ikon ) di bawah browser</span>
              </li>
              <li className="flex gap-3">
                <span className="font-semibold text-emerald-600">2.</span>
                <span>Scroll dan pilih <strong>"Add to Home Screen"</strong></span>
              </li>
              <li className="flex gap-3">
                <span className="font-semibold text-emerald-600">3.</span>
                <span>Tap <strong>"Add"</strong> di pojok kanan atas</span>
              </li>
            </ol>
            <button
              onClick={() => {
                setShowModal(false);
                localStorage.setItem('pwa-installed', 'true');
                setIsInstalled(true);
              }}
              className="w-full mt-6 bg-emerald-600 text-white py-3 rounded-lg font-medium hover:bg-emerald-700 transition-colors"
            >
              Mengerti
            </button>
          </div>
        </div>
      )}

      <style>{`
        @keyframes slide-up {
          from {
            transform: translateY(100%);
          }
          to {
            transform: translateY(0);
          }
        }
        .animate-slide-up {
          animation: slide-up 0.3s ease-out;
        }
      `}</style>
    </>
  );
};

export default InstallPrompt;
