import React, { useState, useRef } from 'react';

type PaymentMethod = 'TUNAI' | 'TRANSFER' | 'QRIS';

interface Props {
  total: number;
  paymentAmount: number;
  kembalian: number;
  isSubmitting: boolean; // FIX #1: Loading state from parent
  onShortcutTap: (amount: number) => void;
  onUangPas: () => void;
  onManualInput: (amount: number) => void;
  onReset: () => void;
  onComplete: (paymentMethod: PaymentMethod, proofImage?: string) => void;
  onCancel: () => void;
}

// FIX #5: Compact shortcuts - only 4 items in 1 row
const PAYMENT_SHORTCUTS = [
  { label: '+10K', value: 10000 },
  { label: '+20K', value: 20000 },
  { label: '+50K', value: 50000 },
  { label: '+100K', value: 100000 },
];

const PaymentSection: React.FC<Props> = ({
  total,
  paymentAmount,
  kembalian,
  isSubmitting,
  onShortcutTap,
  onUangPas,
  onManualInput,
  onReset,
  onComplete,
  onCancel
}) => {
  const [paymentMethod, setPaymentMethod] = useState<PaymentMethod>('TUNAI');
  const [proofImage, setProofImage] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const isValidPayment = paymentMethod === 'TUNAI'
    ? paymentAmount >= total
    : proofImage !== null;

  const handleProofUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (ev) => {
        setProofImage(ev.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleComplete = () => {
    if (isSubmitting) return; // Prevent double-tap
    onComplete(paymentMethod, proofImage || undefined);
  };

  return (
    // CONTAINER FIX: absolute instead of fixed - contained within POS panel
    <div className="payment-section absolute inset-0 bg-black/50 flex items-end z-40">
      {/* Popup - contained within parent */}
      <div className="bg-white w-full rounded-t-2xl shadow-2xl flex flex-col" style={{ maxHeight: '65vh' }}>

        {/* HEADER - Fixed, compact */}
        <div className="flex items-center justify-between p-3 border-b flex-shrink-0">
          <span className="text-gray-500 text-sm">Total</span>
          <span className="text-xl font-bold text-green-600">Rp{total.toLocaleString('id-ID')}</span>
          <button onClick={onCancel} className="text-gray-400 hover:text-gray-600 p-1"></button>
        </div>

        {/* PAYMENT METHODS - Fixed, compact */}
        <div className="flex gap-2 p-3 flex-shrink-0">
          {(['TUNAI', 'TRANSFER', 'QRIS'] as PaymentMethod[]).map(method => (
            <button
              key={method}
              onClick={() => {
                setPaymentMethod(method);
                setProofImage(null);
              }}
              className={`flex-1 py-2 rounded-lg text-sm font-medium transition-colors ${
                paymentMethod === method
                  ? 'bg-green-500 text-white'
                  : 'bg-gray-100 text-gray-700'
              }`}
            >
              {method === 'TUNAI' && ' '}
              {method === 'TRANSFER' && ' '}
              {method === 'QRIS' && ' '}
              {method}
            </button>
          ))}
        </div>

        {/* CONTENT - FIX #3: Fixed height container for each method */}
        <div className="flex-1 px-3 pb-2 overflow-hidden" style={{ minHeight: '180px' }}>

          {/* TUNAI */}
          {paymentMethod === 'TUNAI' && (
            <div className="h-full flex flex-col">
              {/* Quick amounts - 1 ROW */}
              <div className="flex gap-2 mb-2">
                {PAYMENT_SHORTCUTS.map(s => (
                  <button
                    key={s.value}
                    onClick={() => onShortcutTap(s.value)}
                    className="flex-1 py-2 bg-blue-50 hover:bg-blue-100 rounded text-blue-600 text-sm font-medium"
                  >
                    {s.label}
                  </button>
                ))}
              </div>

              {/* Uang Pas */}
              <button
                onClick={onUangPas}
                className="w-full py-2 bg-green-50 hover:bg-green-100 text-green-600 rounded mb-2 text-sm font-medium"
              >
                 UANG PAS
              </button>

              {/* Manual input - compact */}
              <div className="flex items-center gap-2 mb-2">
                <span className="text-gray-500 text-sm">Rp</span>
                <input
                  type="text"
                  inputMode="numeric"
                  value={paymentAmount || ''}
                  onChange={(e) => onManualInput(parseInt(e.target.value.replace(/\D/g, '')) || 0)}
                  className="flex-1 py-2 px-3 border rounded text-right font-medium"
                  placeholder="0"
                />
                <button
                  onClick={onReset}
                  className="text-gray-400 text-sm px-2 py-1 hover:bg-gray-100 rounded"
                >
                  Reset
                </button>
              </div>
            </div>
          )}

          {/* TRANSFER */}
          {paymentMethod === 'TRANSFER' && (
            <div className="h-full flex items-center justify-center">
              <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                capture="environment"
                onChange={handleProofUpload}
                className="hidden"
                id="transfer-proof"
              />
              {proofImage ? (
                <div className="relative">
                  <img src={proofImage} alt="Bukti" className="max-h-32 rounded-lg" />
                  <button
                    onClick={() => setProofImage(null)}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full w-6 h-6 text-sm"
                  >
                    
                  </button>
                  <p className="text-green-600 text-sm mt-2 text-center"> Foto terupload</p>
                </div>
              ) : (
                <label
                  htmlFor="transfer-proof"
                  className="flex flex-col items-center gap-2 p-6 border-2 border-dashed rounded-xl cursor-pointer hover:border-green-500"
                >
                  <span className="text-3xl"></span>
                  <span className="text-gray-600 text-sm">Foto Bukti Transfer</span>
                </label>
              )}
            </div>
          )}

          {/* QRIS */}
          {paymentMethod === 'QRIS' && (
            <div className="h-full flex items-center justify-center">
              <input
                type="file"
                accept="image/*"
                capture="environment"
                onChange={handleProofUpload}
                className="hidden"
                id="qris-proof"
              />
              {proofImage ? (
                <div className="relative">
                  <img src={proofImage} alt="Bukti" className="max-h-32 rounded-lg" />
                  <button
                    onClick={() => setProofImage(null)}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full w-6 h-6 text-sm"
                  >
                    
                  </button>
                  <p className="text-green-600 text-sm mt-2 text-center"> Foto terupload</p>
                </div>
              ) : (
                <label
                  htmlFor="qris-proof"
                  className="flex flex-col items-center gap-2 p-6 border-2 border-dashed rounded-xl cursor-pointer hover:border-green-500"
                >
                  <span className="text-3xl"></span>
                  <span className="text-gray-600 text-sm">Foto Bukti QRIS</span>
                </label>
              )}
            </div>
          )}
        </div>

        {/* SUMMARY - Fixed, compact */}
        {paymentMethod === 'TUNAI' && (
          <div className="px-3 py-2 bg-gray-50 text-sm flex-shrink-0">
            <div className="flex justify-between">
              <span className="text-gray-500">Dibayar</span>
              <span className="font-medium">Rp{paymentAmount.toLocaleString('id-ID')}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500">Kembalian</span>
              <span className={kembalian >= 0 ? 'text-green-600 font-bold' : 'text-red-500 font-bold'}>
                {kembalian >= 0 ? `Rp${kembalian.toLocaleString('id-ID')}` : 'Kurang!'}
              </span>
            </div>
          </div>
        )}

        {/* BUTTON - Fixed bottom, FIX #1: Loading state */}
        <div className="p-3 flex-shrink-0" style={{ paddingBottom: 'max(12px, env(safe-area-inset-bottom))' }}>
          <button
            onClick={handleComplete}
            disabled={isSubmitting || !isValidPayment}
            className={`w-full py-3 rounded-xl font-bold text-white transition-colors ${
              isSubmitting
                ? 'bg-gray-400 cursor-wait'
                : isValidPayment
                  ? 'bg-green-600 hover:bg-green-700 active:bg-green-800'
                  : 'bg-gray-300 cursor-not-allowed'
            }`}
          >
            {isSubmitting ? (
              <span className="flex items-center justify-center gap-2">
                <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24" fill="none">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"/>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4z"/>
                </svg>
                Memproses...
              </span>
            ) : (
              ' Selesai'
            )}
          </button>
        </div>

      </div>
    </div>
  );
};

export default PaymentSection;
