import React, { useState } from 'react';
import { CartItem } from './types';

interface Props {
  items: CartItem[];
  total: number;
  onQtyChange: (itemId: string, newQty: number) => void;
  onRemove: (itemId: string) => void;
  onCheckout: () => void;
}

const CartSection: React.FC<Props> = ({ items, total, onQtyChange, onRemove, onCheckout }) => {
  const [editingId, setEditingId] = useState<string | null>(null);
  const [editValue, setEditValue] = useState('');

  const handleQtyTap = (item: CartItem) => {
    setEditingId(item.id);
    setEditValue(item.qty.toString());
  };

  const handleQtySubmit = (itemId: string) => {
    const newQty = parseInt(editValue) || 0;
    onQtyChange(itemId, newQty);
    setEditingId(null);
    setEditValue('');
  };

  const formatPrice = (price: number): string => {
    if (price >= 1000) {
      return `${(price / 1000).toFixed(0)}K`;
    }
    return price.toString();
  };

  return (
    <div className="cart-section flex-1 flex flex-col bg-white relative">
      {/* Cart Header */}
      <div className="cart-header px-4 py-2 bg-gray-50 border-y border-gray-200 flex justify-between items-center">
        <span className="font-semibold text-gray-700"> Keranjang ({items.length})</span>
        <span className="font-bold text-green-600">
          Rp{total.toLocaleString('id-ID')}
        </span>
      </div>

      {/* Cart Items - more space, account for sticky button */}
      <div className="cart-items flex-1 overflow-y-auto pb-24" style={{ maxHeight: 'calc(100vh - 300px)' }}>
        {items.length === 0 ? (
          <div className="text-center text-gray-400 py-8">
            Keranjang kosong<br/>
            <span className="text-sm">Scan barcode untuk mulai</span>
          </div>
        ) : (
          items.map(item => (
            <div
              key={item.id}
              className="cart-item flex items-center justify-between px-4 py-2 border-b border-gray-100"
            >
              {/* Item Info */}
              <div className="flex-1 min-w-0 mr-2">
                <div className="font-medium text-sm truncate">{item.name}</div>
                <div className="text-xs text-gray-500">
                  {formatPrice(item.price)}  {item.qty}
                </div>
              </div>

              {/* Qty Controls [] qty [+] */}
              <div className="flex items-center gap-1">
                {/* Minus Button */}
                <button
                  onClick={() => onQtyChange(item.id, item.qty - 1)}
                  className="w-8 h-8 bg-gray-200 hover:bg-gray-300 active:bg-gray-400 rounded-full flex items-center justify-center text-gray-700 font-bold transition-colors"
                >
                  
                </button>

                {/* Qty Display (tap to edit) */}
                {editingId === item.id ? (
                  <input
                    type="number"
                    value={editValue}
                    onChange={(e) => setEditValue(e.target.value)}
                    onBlur={() => handleQtySubmit(item.id)}
                    onKeyDown={(e) => e.key === 'Enter' && handleQtySubmit(item.id)}
                    className="w-12 text-center border border-amber-400 rounded py-1 text-sm focus:outline-none focus:ring-2 focus:ring-amber-500"
                    autoFocus
                    inputMode="numeric"
                  />
                ) : (
                  <button
                    onClick={() => handleQtyTap(item)}
                    className="w-10 h-8 bg-gray-100 rounded text-center font-semibold text-sm hover:bg-gray-200 transition-colors"
                  >
                    {item.qty}
                  </button>
                )}

                {/* Plus Button */}
                <button
                  onClick={() => onQtyChange(item.id, item.qty + 1)}
                  className="w-8 h-8 bg-green-500 hover:bg-green-600 active:bg-green-700 rounded-full flex items-center justify-center text-white font-bold transition-colors"
                >
                  +
                </button>

                {/* Subtotal */}
                <span className="w-16 text-right font-semibold text-sm text-gray-700">
                  {formatPrice(item.price * item.qty)}
                </span>

                {/* Remove Button */}
                <button
                  onClick={() => onRemove(item.id)}
                  className="text-red-400 hover:text-red-600 text-lg px-1 transition-colors"
                >
                  
                </button>
              </div>
            </div>
          ))
        )}
      </div>

      {/* Checkout Button - Fixed at bottom */}
      {items.length > 0 && (
        <div className="cart-footer fixed bottom-0 left-0 right-0 p-4 bg-white border-t border-gray-200 shadow-lg" style={{ paddingBottom: 'max(16px, env(safe-area-inset-bottom))' }}>
          <button
            onClick={onCheckout}
            className="w-full bg-green-600 hover:bg-green-700 active:bg-green-800 text-white py-4 rounded-xl font-bold text-lg transition-colors shadow-md"
          >
             Bayar - Rp{total.toLocaleString('id-ID')}
          </button>
        </div>
      )}
    </div>
  );
};

export default CartSection;
