import React, { useState, useEffect, useMemo } from 'react';
import Fuse from 'fuse.js';

interface Supplier {
  name: string;
  contact?: string | null;
}

interface SupplierAutocompleteProps {
  value: string;
  onChange: (value: string) => void;
  placeholder?: string;
  className?: string;
  disabled?: boolean;
}

export const SupplierAutocomplete: React.FC<SupplierAutocompleteProps> = ({
  value,
  onChange,
  placeholder = "Ketik nama supplier...",
  className = "",
  disabled = false
}) => {
  const [allSuppliers, setAllSuppliers] = useState<Supplier[]>([]);
  const [isInitialLoading, setIsInitialLoading] = useState(true);
  const [selectedIndex, setSelectedIndex] = useState(-1);
  const [fuse, setFuse] = useState<Fuse<Supplier> | null>(null);

  // Prefetch ALL suppliers on mount (one-time)
  useEffect(() => {
    const fetchAllSuppliers = async () => {
      setIsInitialLoading(true);
      try {
        const token = localStorage.getItem('access_token');
        const response = await fetch('/api/suppliers/all', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (!response.ok) throw new Error('Failed to fetch suppliers');

        const data = await response.json();
        setAllSuppliers(data);

        // Initialize fuzzy search
        const fuseInstance = new Fuse<Supplier>(data, {
          keys: ['name'],
          threshold: 0.3,
          distance: 100,
          minMatchCharLength: 1
        });
        setFuse(fuseInstance);
      } catch (error) {
        console.error('Failed to fetch suppliers:', error);
      } finally {
        setIsInitialLoading(false);
      }
    };

    fetchAllSuppliers();
  }, []);

  // INSTANT client-side filtering (no API call!)
  const filteredSuppliers = useMemo(() => {
    if (!value || !fuse) {
      // Show top 10 if no query
      return allSuppliers.slice(0, 10);
    }

    // Fuzzy search
    const results = fuse.search(value);
    return results.map(r => r.item).slice(0, 10);
  }, [value, fuse, allSuppliers]);

  const handleSelect = (supplier: Supplier) => {
    onChange(supplier.name);
  };

  // Initial loading state (only shown once on mount)
  if (isInitialLoading) {
    return (
      <div className="p-4 text-center text-gray-500">
        <svg className="animate-spin h-5 w-5 mx-auto text-amber-500" fill="none" viewBox="0 0 24 24">
          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
        </svg>
        <div className="mt-2 text-sm">Loading supplier...</div>
      </div>
    );
  }

  // No results
  if (filteredSuppliers.length === 0) {
    return (
      <div className="p-4 text-center text-gray-500">
        <svg className="w-8 h-8 mx-auto mb-2 opacity-30" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
        </svg>
        <div className="text-sm">{value ? 'Supplier tidak ditemukan' : 'Belum ada supplier'}</div>
        {value && (
          <button
            onClick={() => onChange(value)}
            className="mt-2 text-xs text-amber-600 hover:text-amber-700"
          >
            Tambah "{value}" sebagai supplier baru
          </button>
        )}
      </div>
    );
  }

  // Show suggestions - INSTANT (no loading spinner during filtering)
  return (
    <div className="p-2 max-h-80 overflow-y-auto">
      <div className="text-xs text-gray-500 mb-2 px-2">
        {value ? ` ${filteredSuppliers.length} hasil:` : ' Supplier Sering Dipakai:'}
      </div>
      <div className="space-y-0.5">
        {filteredSuppliers.map((supplier, index) => (
          <button
            key={`${supplier.name}-${index}`}
            onClick={() => handleSelect(supplier)}
            className={`w-full px-3 py-2 text-left hover:bg-amber-50 flex items-center justify-between rounded transition-colors ${
              index === selectedIndex ? 'bg-amber-50' : ''
            }`}
          >
            <div className="flex items-center gap-2">
              <svg className="w-4 h-4 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
              </svg>
              <div>
                <div className="font-medium text-sm">{supplier.name}</div>
                {supplier.contact && (
                  <div className="text-xs text-gray-500">{supplier.contact}</div>
                )}
              </div>
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};

export default SupplierAutocomplete;
