import React from 'react';
import { Product } from './types';

interface Props {
  products: Product[];
  onSelect: (product: Product) => void;
}

const RecentProducts: React.FC<Props> = ({ products, onSelect }) => {
  const formatPrice = (price: number): string => {
    if (price >= 1000) {
      return `${(price / 1000).toFixed(0)}K`;
    }
    return price.toString();
  };

  if (products.length === 0) return null;

  return (
    <div className="recent-products px-4 py-2 border-b border-gray-100">
      <div className="text-xs text-gray-500 mb-2"> Produk Terakhir</div>
      <div className="flex gap-2 overflow-x-auto pb-2 -mx-1 px-1">
        {products.map(product => (
          <button
            key={product.id}
            onClick={() => onSelect(product)}
            className="flex-shrink-0 bg-gray-100 rounded-lg px-3 py-2 text-sm active:bg-gray-200 transition-colors"
          >
            <div className="font-medium truncate max-w-24 text-left">{product.name}</div>
            <div className="text-green-600 text-xs text-left">{formatPrice(product.price)}</div>
          </button>
        ))}
      </div>
    </div>
  );
};

export default RecentProducts;
